.aui-blanket[aria-hidden=false] {
  opacity: @blanket_opacity_default;
  // AUI is trying to transition opacity AND visibility, but we can only
  // transition opacity, so overriding AUI here.
  transition-property: opacity;
}

.hc-dialog-btn-spinner {
  display: inline-block;
  position: relative;
  float: left;
  height: 30px;
  width: 20px;

  .hc-spinner {
    position: absolute;
    top: 50%;
  }
}

.aui-dialog2 {
  h2,
  h3,
  legend {
    cursor: default;
  }

  &.modal-dialog-alt {
    visibility: visible;

    div {
      height: auto;
    }

    & + .aui-blanket {
      background-color: @white;
      &[aria-hidden=false] {
        opacity: @blanket_opacity_more;
      }
    }

    .aui-dialog2-content {
      border-radius: 5px;
      border: solid 3px @blueBorder;
      text-align: center;
      box-sizing: border-box;
      .user-select(none);
      cursor: default;
      max-height: inherit;

      h2 {
        color: @darkBlueText;
        font-weight: 500;
        font-size: 20px;
        margin-top: 0;
      }

      img {
        margin: 0 auto;
        max-height: 150px;
        max-width: 150px;
      }

      .inner-content {
        padding: 15px 0px;
        margin: 10px 0;

        ul {
          text-align: left;
        }
      }
    }
  }
}

.aui-dialog2-content {
  &.hc-dialog-content {
    height: auto;
  }
}

.aui-dialog2-medium {
  .aui-dialog2-content {
    min-height: 85px;
    box-sizing: content-box;
  }
}

.aui-dialog2-small {
  .aui-dialog2-header-main {
    max-width: 290px;
  }
}


/*** CUSTOM DIALOGS ***/
#room-notifications-dialog {
  width: 450px;

  .prefix, .settings-info {
    padding-left: 5px;
  }

  .settings-info {
    margin-top: 3px;
    font-size: 12px;
  }

  .room-name {
    margin-left: 2px;
    font-weight: 500;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
    width: 220px;
    display: inline-block;
    vertical-align: bottom;
  }

  .heading {
    margin-bottom: 12px;
  }

  .aui-dialog2-header-main .aui-lozenge {
    margin-left: 5px;
    position: relative;
    top: -2px;
  }
}

#release-notes-dialog {
  iframe {
    border: none;
    width: 100%;
  }
}

#desktop-4-dialog {
  top: 7%;
  min-width: 450px;

  div.img {
    margin: 0 auto;
    background-size: 100%;
    width: 150px;
    height: 137px;
    margin-bottom: 15px;
  }
}