pre.prettyprint {
  border: none;
  border-radius: 3px;
  display: block;
  padding: 10px;
  white-space: pre-wrap;
}
pre .nocode {
  background-color: none;
}
pre.prettyprint.linenums {
  padding: 0 0 0 4px;
  &:empty:after {
    content: "";
  }
}
ol.linenums {
  border-radius: 3px;
  padding-top: 10px;
  padding-bottom: 10px;
  li.L0, li.L1, li.L2, li.L3, li.L5, li.L6, li.L7, li.L8 {
    list-style-type: decimal !important
  }
  li, .prettyprint {
    background: none;
  }
}

.prettify-colors-light();
.dark {
  .prettify-colors-dark();
}
