#hipchat, body {
  .hc-chat-box {
    padding: 8px;
    background-color: @bggray;
    border-top: solid 1px @bordergray;
    border-right: 1px solid @bordergray;
    box-sizing: border-box;
    min-height: @chat-box-height;
    user-select: none;
    z-index: 2;
    width: 100%;

    &.sidebar-hidden {
      width: 100%;
    }

    form.aui {
      white-space: nowrap;
      height: 100%;

      table, td {
        border-spacing: 0;
        margin: 0;
        padding: 0;
      }

      .aui-form-notification-tooltip {
        left: -5px;
        white-space: normal;
        transition: opacity 200ms ease-in-out;
      }

      .hc-text-input-autocomplete-container{
        position: relative;
      }

      .hc-text-input {
        white-space: pre-wrap; // fixes textarea wrapping in IE
        position: relative;
        &.hc-file-sharing-disabled{
          .hc-textarea {
            border-left: 1px solid @bordergray;
            border-radius: 3px;
          }
        }
        .hc-textarea {
          display: block;
          border-left: none;
          .box-shadow(none);
          outline: none;
          resize: none;
          max-width: 100%;
          border-radius: 0 3px 3px 0;
          height: 30px;
          min-height: 30px;
          max-height: 200px;
          padding-right: 25px; // prevent text from extending beneath smiley
        }
        .smiley-icon {
          position: absolute;
          top: 50%;
          right: 0;
          transform: translateY(-50%);
          padding: 3px 7px;
          cursor: pointer;
          color: @subdued;
          z-index: 5;
          &:hover, &.selected {
            color: @lightblue;
          }
        }
        &.hc-textarea-has-scroll {
          .hc-textarea{
            padding-right: 38px;
          }
          .smiley-icon {
            right: 15px;
          }
        }
      }

      .hc-file-btn,
      .input_actions_trigger button#input_actions_dropdown-trigger.hc-dropdown-trigger.aui-dropdown2-trigger.aui-button.aui-button-subtle {
        border: 1px solid @bordergray;
        border-radius: 3px 0 0 3px;
        background: white;
        align-items: stretch;

        &:hover {
          border-color: @darkerborder;
          cursor: pointer;
        }

        &.active {
          border: 1px solid @bordergray;
          border-radius: 3px 0 0 3px;
          padding-top: 4px;
          padding-bottom: 4px;
          background-color: @bordergray;

          &:hover {
            border: 1px solid @darkerborder;
            border-radius: 3px 0 0 3px !important;
            background-color: @bordergray;
          }
        }

        &.has-file {
          padding: 4px 10px;
          &:hover {
            border-color: @bordergray;
            cursor: default;
          }
          .hc-attach {
            padding: 0;
            vertical-align: middle;
            height: 16px;
            display: inline-flex;
          }
        }

        .hc-attach {
          color: @subdued;
          display: inline-block;
          padding: 4px 12px;
          height: 100%;
          display: flex;
          box-sizing: border-box;
          .hipchat-icon-small {
            height: 100%;
            vertical-align: middle;
          }

          &:active {
            .box-shadow(inset 0 3px 6px 0 rgba(0, 0, 0, .1));
            background: @bggray;
          }
        }
      }

      #hc-chat-actions.input_actions_trigger {
        .hc-dropdown {
          height: 100%;
        }

        button#input_actions_dropdown-trigger.hc-dropdown-trigger.aui-dropdown2-trigger.aui-button.aui-button-subtle {
          height: 100%;
          margin-right: 0;
          padding-left: 12px;
          padding-right: 12px;

          .aui-icon.aui-icon-small  {
            margin-right: 0;
          }
        }
      }

      .upload-input {
        width: 180px;
        display: inline-block;
        input {
          .box-shadow(none);
          width: 140px;
          border-radius: 3px;
          border: 1px solid @bordergray;
          outline: none;
          resize: none;
          margin-left: 10px;
          padding: 0px 4px;
          &.error {
            border-color: @red;
          }
        }
        .aui-progress-indicator {
          margin-left: 10px;
          width: 170px;
          position: relative;
          top: -1px;
        }
        .close-upload {
          color: @subdued;
          padding: 0 10px;
          span {
            height: 10px;
            width: 10px;
            vertical-align: middle;
            &:after {
              margin-top: -5px;
              font-size: 10px;
            }
          }
        }
      }
    }

    #hc-chat-actions {
      height: 100%;
      width: 1%;
    }
  }
}

div#input_actions_dropdown.aui-style-default.aui-dropdown2.hc-dropdown-container.hc-dropdown-above {
  border-radius: 3px 3px 3px 3px;
  margin-top: -13px;
}



#hc-chat-actions-arrow {
  width: 1px;
  position: absolute;
  height: 16px;
  bottom: -16px;
  left: 10px;

  &::before, &::after {
    border-color: #ccc transparent;
    border-style: solid;
    border-width: 8px 8px 0;
    content: "";
    position: absolute;
    top: 0;
  }

  &:after {
    border-top-color: @white;
    top: -1px;
  }
}

