body {
  &.tighter {
    #hipchat {
      .hc-main-col {
        .hc-rooms-container{
          .hc-chat-panel-header-container{
            min-height: @chat-panel-header-height-tighter;
          }
        }
      }

      .aui-navgroup-inner {
        padding-top: 0;
      }
      .aui-navgroup-inner .hc-sidebar-nav.hc-lobby {
        margin-top: 10px;
      }
      .aui-navgroup-inner .hc-sidebar-nav .aui-nav-heading {
        padding: 5px 10px 0;
      }
      .hc-tab .aui-nav-item, body .hc-tab .aui-nav-item {
        padding-top: 4px;
        padding-bottom: 5px;
      }
      .aui-page-header.room-header {
        padding: 5px;
        height: 60px;
      }
      .aui-page-header.room-header .aui-page-header-actions {
        top: 15px;
      }
      .hc-tab-close {
        padding-top: 5px;
        padding-bottom: 3px;
        top: 1px;
        span {
          height: 16px;
        }
      }
      .date-divider {
        margin: 10px 0;
        span {
          font-size: 12px;
        }
      }
      .hc-chat-row {
        padding: 4px 0;
      }
      .hc-chat-msg .msg-line {
        margin-bottom: 2px;
      }
      .hc-chat-msg .unread-line {
        margin-bottom: -4px;
      }
    }
  }
}
