#header .aui-button-link.hc-invite-users {
  color: @white;
}

#invite-teammates-dialog[aria-hidden=false] {
  display: block;
}

#invite-teammates-dialog{
  height: calc(~"100% - 101px");
  max-height: 520px;

  &.aui-dialog2.aui-layer{
    /* 169px normally - extra 68px for header that is not shifted up in this iframed guy */
    top: 101px;
  }

  .aui-dialog2-content{
    overflow: hidden;
    padding: 0;
    height: 100%;
    background-color: transparent;
    iframe {
      width: 100%;
      height: 100%;
      border-radius: 5px;
      position: absolute;
      border: none;
      visibility: hidden;

      &.loaded {
        visibility: visible;
      }
    }
  }
}