svg.aui-icon {
  fill: currentColor;
}

#icon-dnd, #icon-dnd-selected, #icon-mobile-selected, #icon-available-selected, #icon-unavailable-selected {
  .path1 {
    fill: @bggray;
  }
}

#icon-dot .path1 {
  fill: @icon;
}

#icon-available .path1, #icon-available-selected .path2 {
  fill: @green;
}

#icon-away .path2, #icon-away-selected .path1, #icon-away-selected .path3, #icon-xa .path2, #icon-xa-selected .path1, #icon-xa-selected .path3 {
  fill: @bggray;
}

#icon-away .path1, #icon-away-selected .path2, #icon-xa .path1, #icon-xa-selected .path2 {
  fill: @orange;
}

#icon-dnd .path1, #icon-dnd-selected .path2 {
  fill: @red;
}

#icon-mobile .path1, #icon-mobile-selected .path2 {
  fill: @mediumtext;
}

#icon-mobile .path2, #icon-mobile-selected .path3 {
  fill: @codebg;
}

#icon-unavailable .path2, #icon-unavailable-selected .path3 {
  fill: @codebg;
}

body > #hipchat svg.hc-status-icon {
  width: 16px;
  height: 16px;
  top: 0;
}

#icon-mobile-selected .path1 {
  fill: @white;
}

.aui-select2-container.select2-container .select2-choice abbr,
.aui-select2-container.select2-container .select2-search-choice-close {
  background: url('data:image/png;base64,') !important;
  background-size: 8px !important;
}

.hc-room-notif-icon {
  display: inline-block;
  margin: 5px;
  opacity: 0.75;
}

.hc-room-notif-icon:hover {
  cursor: pointer;
  opacity: 1;
}