.ap-aui-dialog2.aui-dialog2.aui-layer {
    height: 100%;
}

.ap-aui-dialog2 iframe, #connect-dialog-container {
    transition:width 200ms ease-in-out, height 200ms ease-in-out;
}

.ap-aui-dialog2 .ap-stats {
    bottom: 0;
    width: ~"calc(100% - 20px)";
}

#connect-dialog-container {
  .hc-addon-iframe {
    flex: 1;
  }

  .hc-dialog-content {
    display: flex;
    padding: 0;
  }
}