@import "../colors";
@import "../dark_colors";

/* ADG AA Light Scheme for google prettify */
.prettify-colors-light () {
  ol.linenums { background: @white; color: @adgAshGray; }
  pre.prettyprint { background-color: @white; }
  pre .nocode { color: @white; }
  pre .str { color: #c42b44; } /* string - ADG AA Pink Der */
  pre .kwd { color: @adgNavy; } /* ADG AA Navy */
  pre .com { color: #0C70AA; } /* comment - ADG AA Cyan Der */
  pre .typ { color: #106E23; } /* type - ADG AA Green Der */
  pre .lit { color: #AD5100; } /* literal - ADG AA Orange Der */
  pre .pun { color: @adgCharcoal; } /* punctuation - ADG AA Charcoal */
  pre .pln { color: @adgCharcoal; } /* plaintext - ADG AA Charcoal */
  pre .tag { color: @adgNavy; } /* html/xml tag - ADG AA Navy */
  pre .atn { color: @purple; } /* attribute name - ADG AA Purple Der */
  pre .atv { color: #c42b44; } /* attribute value - ADG AA Pink Der */
  pre .dec { color: #106E23; } /* decimal - ADG AA Green Der */
}

/* ADG AA Dark Scheme for google prettify */
.prettify-colors-dark () {
  ol.linenums { background: @darkChatPanelBg; color: @darkerGrayText; }
  pre.prettyprint { background-color: @black; }
  pre .nocode { color: @black; }
  pre .str { color: #F3788D; } /* string - ADG AA Pink Der */
  pre .kwd { color: #399CFF; } /* ADG AA Navy Der */
  pre .com { color: #91D7FF; } /* comment - ADG AA Cyan Der */
  pre .typ { color: @green; } /* type - ADG AA Green Der */
  pre .lit { color: @orange; } /* literal - ADG AA Orange Der */
  pre .pun { color: #B3B3B3; } /* punctuation - ADG AA Charcoal Der */
  pre .pln { color: #B3B3B3; } /* plaintext - ADG AA Charcoal Der */
  pre .tag { color: #399CFF; } /* html/xml tag - ADG AA Navy Der */
  pre .atn { color: #B290D6; } /* attribute name - ADG AA Purple Der */
  pre .atv { color: #F3788D; } /* attribute value - ADG AA Pink Der */
  pre .dec { color: @green; } /* decimal - ADG AA Green Der */
}
