#settings-dialog {

  .aui-navgroup {
    width: 160px;
    height: 300px;
    border-right: 1px solid #ccc;
    padding: 0 20px 0 0;
    display: table-cell;
  }

  .inline-field-group {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    clear: both;
    position: relative;
    margin: 1px 0;
  }

  .field-group {
    &.label-position-right {
      display: flex;
      flex-direction: row-reverse;
      padding: 4px 0 4px 20px;
      width: auto;

      label {
        margin-left: 8px;
        width: auto;
      }

      .error {
        margin-left: 8px;
        order: -1;
      }

    }
  }

  h3 {
    margin-top: 5px;
  }

  #room-notifications-form {
    fieldset.group {
      padding-left: 0px;
    }
    .aui-lozenge {
      margin-left: 5px;
      vertical-align: text-bottom;
    }
  }

  form.aui {
    label {
      color: inherit;
    }
    div.checkbox, div.radio {
      margin-left: 2px;
    }
  }

  .checkbox-select2-field {
    margin-top: 14px;
    margin-bottom: 0;

    .select2-container {
      margin-left: 4px;
      margin-right: 4px;
      // Offsets padding-top:4px and border-top-width:1px to keep this text in
      // place with the rest of the label content
      top: -5px;
      width: auto;
      display: inline-block;
    }

    &.disabled {

      .select2-container{
        // AUI 5.7.12 has a bug that keeps the disabled styles from being overwritten,
        // so duplicate here. See https://docs.atlassian.com/aui/5.7.12/sandbox/index.html?component=auiselect2
        pointer-events: none;
        opacity: 0.5;
      }
    }

    .select-title {
      vertical-align: top;
    }
  }

  .sounds-preference .select2-container {
    max-width: 180px;
  }
  .mac-dock-bounce-preference .select2-container {
    max-width: 100px;
  }
}
