.file-error {

  &-message {
    display: inline-block;
    max-width: 315px;
    margin: 5px 10px 5px 0;
    padding: 22px 20px;
    background-color: @white;
    font-size: 13px;
    color: @fileErrorColor;
    border: 1px solid @adgAshGray;
    border-radius: 3px;

    h3 {
      color: @fileErrorColor;
      font-weight: 700;
      font-size: 13px;
    }
  }

  &-spinner {
    margin: auto;
    width: 20px;
    height: 20px;
    position: relative;
    top: 18px;
  }
}
