@dialog-border-radius: 5px;

#incoming-call-dialog {
  width: 250px;
  height: 330px;
  .aui-dialog2-content {
    padding: 0;
    border-radius: @dialog-border-radius;
  }
  .incoming-call-dialog {
    position: absolute;
    width: 100%;
    background: @bggray;
    border-radius: @dialog-border-radius;
    text-align: center;
    padding: 20px 10px 5px;
    box-sizing: border-box;
    .call-from {
      color: @icon;
    }
    .caller-name {
      font-weight: 500;
      margin: 5px 0 20px 0;
    }
    button {
      display: inline-block;
      width: 100%;
      margin: 5px 0;
    }
  }
}
