#integrations-management-dialog {
  @dialog-height: 350px;
  @dialog-spacing: 10px;
  @dialog-section-spacing: 20px;
  @dialog-icon-size: 16px;
  @dialog-description-font-size: 12px;

  @hc-install-link-margin: 10px 0 20px;
  @hc-install-link-padding: 0 20px;
  @hc-install-link-height: 40px;

  @integration-item-height: 40px;
  @integration-item-padding: 0 20px;

  @empty-state-text-width: 400px;
  @empty-state-img-width: 200px;
  @empty-state-img-height: 200px;

  .hc-dialog-content {
    height: @dialog-height;
    overflow-y: auto;
    overflow-x: hidden;
    cursor: default;
  }

  .hc-integration-section-title {
    display: flex;

    h5 {
      flex: 0 1 auto;
    }

    .hc-integrations-installed-count {
      flex: 0 0 auto;
      display: inline-block;
      margin-left: auto;
      text-align: right;
    }
  }

  .hc-integration-section-description {
    display: flex;
    font-size: @dialog-description-font-size;
    margin-top: @dialog-spacing;
    color: @adgMediumGray;

    p {
      margin: 0;
      flex: 0 1 auto;
    }

    .aui-button {
      flex: 0 0 auto;
      margin-left: auto;
    }
  }

  .hc-integrations-empty-state {
    overflow: hidden;
    text-align: center;

    p {
      margin: @dialog-section-spacing auto;
      width: @empty-state-text-width;
    }

    .hc-integrations-es-img {
      margin: 0 auto;
      width: @empty-state-img-width;
      height: @empty-state-img-height;
    }
  }

  .hc-install-link {
    display: flex;
    margin: @hc-install-link-margin;
    padding: @hc-install-link-padding;
    height: @hc-install-link-height;
    border: 1px dashed @bordergray;
    border-radius: 3px;
    cursor: pointer;
    align-items: center;

    .aui-icon {
      flex: 0 0 @dialog-icon-size;
    }

    &:hover {
      background: @adgLightGray;
      text-decoration: none;
    }
  }

  .hc-integrations-list {
    margin: 0 0 @dialog-section-spacing 0;
    padding: 0;
    list-style: none;

    li {
      display: flex;
      margin: @dialog-spacing 0;
      height: @integration-item-height;
      line-height: @integration-item-height;
      padding: @integration-item-padding;
      background: @white;
      border: 1px solid @bordergray;
      border-radius: 3px;
      cursor: default;
      align-items: center;

      img {
        flex: 0 0 @dialog-icon-size;
        margin-right: @dialog-spacing;
      }

      .hc-integration-icon {
        flex: 0 0 @dialog-icon-size;
        margin-right: @dialog-spacing;
        width: @dialog-icon-size;
        height: @dialog-icon-size;
      }

      .hc-integration-name {
        flex: 0 1 auto;
        margin-right: @dialog-spacing;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .hc-integration-management-right {
        flex: 0 0 auto;
        display: inline-block;
        margin-left: auto;

        .hc-integration-configure, .hc-integration-update {
          margin-left: 10px;
        }

        .hc-integration-configure {
          color: @subdued;
        }
      }
    }
  }
}
