#hipchat, body {
  #welcome-dialog {

    .aui-dialog2-header-close {
      display: none;
    }

    .hc-welcome-dialog-title-center {
      text-align: center;
    }

    .hc-welcome-dialog-content {
      height: 280px;
      width: 100%;
      padding: 0;
      float: left;

      .hc-welcome-welcome-img{
        height: 202px;
        width: 229px;
        margin: auto;
      }

      .hc-welcome-welcome-text {
        text-align: center;
        margin-top: 20px;
      }

      .aui-iconfont-star {
        color: @yellow;
      }

      .aui-iconfont-info {
        color: @blue;
      }

      h6 {
        margin-bottom: -5px;
      }

      .hc-welcome-person-item {
        width: 174px;
        display: inline-block;
      }
      .hc-welcome-room-item {
        width: 556px;
      }
      .hc-welcome-person-item:nth-child(n+4) {
        margin-top: 5px;
      }
      .hc-welcome-person-item:nth-child(3n+3) {
        margin-right: 0px;
      }
      .hc-welcome-room-item:last-child {
        margin-bottom: 10px;
      }

      .hc-welcome-person-item,
      .hc-welcome-room-item {
        cursor: pointer;
        height: 48px;
        background-color: @bggray;
        margin-top: 10px;
        margin-right: 13px;
        border-radius: 3px;
        &.selected {
          border: 2px solid @green;
          padding: 0px;
        }
        border: 1px solid #ccc;
        padding: 1px;

        .hc-welcome-room-icon,
        .hc-welcome-person-icon {
          padding: 7px 7px;
          float: left;
          text-align: center;
          .aui-icon {
            color: @icon;
          }
          .icon-public {
            height: 32px;
            width: 32px;
            &:after {
              font-size: 32px;
              margin-top: -16px;
            }
          }
        }

        .hc-welcome-room-info,
        .hc-welcome-person-info {
          padding: 4px 7px 4px 1px;
          float: left;
          height: 36px;
          .hc-welcome-room-name,
          .hc-welcome-person-name {
            color: @text;
          }
          .hc-welcome-room-topic,
          .hc-welcome-person-job-title {
            color: @mediumtext;
            font-size: .9em;
            line-height: 0.9;
          }
        }

        .hc-welcome-room-participants {
          margin: 12px 0px;
          float: right;
          span {
            margin-right: 7px;
          }
        }

        .hc-welcome-person-info-ellipsis {
          width: 110px;
        }
        .hc-welcome-room-info-ellipsis {
          width: 300px;
        }
        .hc-welcome-room-info-ellipsis,
        .hc-welcome-person-info-ellipsis {
          overflow:hidden;
          white-space:nowrap;
          text-overflow: ellipsis;
          color: @mediumtext;
        }
      }

      .hc-welcome-message {
        width: 544px;
        border: 1px solid #ccc;
        border-radius: 3px;
        margin-top: 10px;
        font-size: 1em;
        padding: 5px 7px;

        textarea {
          width: 520px;
          font-family: inherit;
          font-size: inherit;
          resize: none;
          border: none;
          overflow: hidden;
        }
        textarea:focus {
          outline: 0px;
        }

        .hc-welcome-smiley-icon {
          position: absolute;
          top: 150px;
          right: 22px;
          transform: translateY(-50%);
          padding: 3px 7px;
          cursor: pointer;
          color: @subdued;
          z-index: 5;
          &:hover, &.selected {
            color: @lightblue;
          }
          &.star-toggled {
            top: 180px;
          }
        }
      }
      .error {
        margin: 5px 0 0px 0;
        color: @red;
      }
    }

    .hc-loading-spinner {
      .spinner {
        right: -20px;
      }
    }

    .hc-welcome-footer-buttons {
      display: inline-block;

      .aui-button-link {
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}
