#hipchat, body {

  .hc-lobby-panel {
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    user-select: none;
    .flex(1);

    .aui-page-panel-inner{
      position: absolute;
    }

    & > div {
      position: absolute;
      box-sizing: border-box;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
  }
  .hc-lobby-panel-content {
    width: 100%;
    padding: 0;
    float: left;
  }
  .hc-lobby-panel-content .hc-lobby-list-item {
    width: 100%;
    cursor: pointer;
    height: 48px;
    &.selected {
      background-color: @selectedbg;
    }
    border-bottom: 1px solid #ccc;
  }
  .hc-lobby-panel-content .hc-lobby-list-item:first-child > div {
    border-top: 0;
  }
  .hc-lobby-list-icon,
  .hc-lobby-list-names {
    padding: 12px 7px;
    float: left;
    height: 24px;
  }

  .hc-lobby-list-mention-name {
    color: @mediumtext;
    margin-left: 10px;
    font-size: .9em;
    line-height: 0.9;
  }
  .hc-lobby-list-icon {
    padding-left: 14px;
    width: 24px;
    text-align: center;
    .aui-icon {
      color: @icon;
      height: 24px;
      width: 24px;
      &:after {
        font-size: 24px;
        margin-top: -12px;
      }
    }
  }
  .hc-lobby-list-actions {
    text-align: right;
  }
  .hc-lobby-list-name {
    padding-left: 0;
    display: inline-block;
    vertical-align: middle;
  }
  .empty.empty-lobby{
    bottom: inherit;
    top: 80px;
    .empty-lobby-state-img{
      height: 66px;
      width: 70px;
    }
    .empty-search-lobby-state-img{
      height: 62px;
      width: 70px;
      margin-top: 8px;
    }
  }

  .hc-lobby-search-content.aui-page-header-main {
    padding: 0;
  }

}