@quick_switcher_animation_time: 0.3s;
@quick_switcher_width: 320px;

@quick_switcher_hint_border_color: #F88A10;
@quick_switcher_hint_text_color: #818181;
@quick_switcher_hint_close_btn_color: #6A6A6A;

@quick_switcher_selected_border: #3572B0;


#hipchat {

  .hc-qs {
    border-radius: 5px;
    box-sizing: border-box;
    left: 0;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    position: fixed;
    right: 0;
    top: 30%;
    width: @quick_switcher_width;
    z-index: 3000;

    backface-visibility: hidden;
    transition: all @quick_switcher_animation_time;

    &[aria-hidden=true] {
      opacity: 0;
      transform: translateY(-20px);
    }
    &[aria-hidden=false] {
      opacity: 1;
      transform: translateY(0);
    }

    .hipchat-icon-medium {
      height: 32px;
      width: 32px;
      &:after {
        font-size: 32px;
        margin-top: -17px;
      }
    }

    .hc-qs-content {
      border-radius: inherit;
      border: solid 1px @bordergray;
      background-color: @white;
      height: auto;
      max-height: 100%;
      overflow: hidden;

      .hc-qs-input-wrap {
        padding: 6px 25px 6px 55px;

        label {
          position: absolute;
          top: 5px;
          left: 6px;
          color: @adgAshGray;
          user-select: none;
          &:hover {
            cursor: text;
          }
        }

        .hc-qs-input {
          border-color: @white;
          color: @adgCharcoal;

          &:focus {
            border-color: @white;
          }
        }

        &::after {
          color: @adgGray;
          content: "\f18d";
          font-family: "Atlassian Icons";
          height: 16px;
          position: absolute;
          left: 25px;
          top: 11px;
          width: 16px;
          font-size: 16px;
        }
      }

      .hc-qs-list-wrap {
        border-radius: 0 0 4px 4px;
        max-height: 236px;
        overflow-y: auto;

        &.with-hint{
          border-radius: 0;
        }

        .hc-qs-item {
          line-height: 42px;
          overflow: hidden;
          text-overflow: ellipsis;
          color: #999999;
          border-left: 3px solid transparent;
          padding-right: 15px;
          position: relative;
          .ellipsis;

          &.has-badge{
            padding-right: 30px;
          }

          &.hc-qs-empty-state{
            text-align: center;
            color: @subdued;
            font-weight: 500;
            padding-bottom: 20px;

            p {
              margin: 0;
              line-height: 25px;
            }
          }

          &:hover:not(.hc-qs-empty-state) {
            cursor: pointer;
          }

          &.selected {
            background-color: @adgSilver;
            &:not(.hc-qs-empty-state){
              border-left-color: @quick_switcher_selected_border;
            }
          }

          .hc-qs-room-icon,
          .aui-avatar,
          .hc-status-icon {
            vertical-align: middle;
          }

          .aui-avatar {
            background-color: @bordergray;
            margin-left: 12px;
            top: -1px;
          }

          .hc-qs-room-icon {
            margin-left: 12px; // emtpy presence icon width - hc-qs-item border width
          }

          .hc-status-icon {
            margin: 0 8px 0 13px;
          }

          .hc-qs-name {
            margin-left: 14px;
            color: @text;
          }
          .hc-qs-mention-name {
            color: @mediumtext;
            margin-left: 5px;
            font-size: 12px;
            line-height: 0.9;
          }
        }
      }
      .hc-qs-hint {
        width: 300px;
        line-height: 38px;
        margin: 0 auto;
        margin-bottom: 8px;
        border: 1px solid @quick_switcher_hint_border_color;
        position: relative;
        border-radius: 5px;

        & > * {
          vertical-align: middle;
        }

        &.with-results{
         margin-top: 10px;
        }

        .hc-qs-hint-key{
          cursor: default;
          padding: 2px 4px;
          text-align: center;
          margin: 10px 4px 0px 4px;
          color: @quick_switcher_hint_border_color;
          border-radius: 5px;

          &.mac-hint-key {
            border: 1px solid @quick_switcher_hint_border_color;
            margin: 10px 8px 0px 8px;
          }
        }

        .hc-qs-hint-text{
          cursor: default;
          color: @quick_switcher_hint_text_color;
        }

        .hc-qs-hint-close{
          top: 12px;
          right: 5px;
          position: absolute;
          color: @quick_switcher_hint_close_btn_color;
          cursor: pointer;
        }
      }
    }
  }
}