.hc-addon-iframe {
    border: 0;
}

#integration-dialog[aria-hidden=false] {
  display: block;
}

#integration-dialog {
  .hc-addon-iframe {
    flex: 1;
    flex-basis: auto;
    width: 100%;
  }

  .hc-dialog-content {
    display: flex;
    padding: 0;
    max-height: none;
  }

  .hc-integration-view-dialog {
    display: flex;
    padding: 0;
    position: relative;
    flex: 1;
    flex-direction: column;

    &.hc-integration-view-dialog-loading {
      min-height: 200px;
    }
  }

  .hc-spinner {
    position: absolute;
    top: 50%;
    left: 50%;
  }

}
