@import "mixins/mixins.less";

#hipchat, body {

  .hc-message {
    border-radius: 3px;
    border-width: 1px;
    line-height: 20px;
    padding-bottom: 20px;
    padding-left: 60px;
    padding-right: 40px;
    padding-top: 20px;
    background: #fff;
    .hc-icon.icon-close {
      background-image: none;
      color: #707070;
      text-indent: inherit;
    }
    .hc-icon.icon-close:before {
      content: "\f11b";
      font-family: "Atlassian Icons";
      font-size: 16px;
      .font-smoothing();
      font-style: normal;
      font-weight: normal;
    }
    .hc-icon.icon-close:hover {
      color: #333333;
    }
  }
  .hc-message:before {
    background-color: #3572b0;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    bottom: 0;
    content: '';
    left: 0;
    position: absolute;
    top: 0;
    width: 40px;
  }
  .hc-message:after {
    color: #ffffff;
    content: "\f16f";
    font-family: "Atlassian Icons";
    font-size: 16px;
    .font-smoothing();
    font-style: normal;
    font-weight: normal;
    left: 12px;
    line-height: 1;
    margin-top: -8px;
    position: absolute;
    speak: none;
    top: 50%;
  }
  .hc-message .hc-icon-success, .hc-message .hc-icon-warning, .hc-message .hc-icon-info, .hc-message .hc-icon-error, .hc-message .hc-icon-hint, .hc-message .hc-icon-generic, .hc-message .icon-generic, .hc-message .icon-error, .hc-message .icon-hint, .hc-message .icon-info, .hc-message .icon-success, .hc-message .icon-warning {
    display: none;
  }
  .hc-message.error, .hc-message-error {
    border: solid 1px #d04437;
  }
  .hc-message.hint, .hc-message-hint, .hc-message.info, .hc-message-info {
    border: solid 1px #3572b0;
  }
  .hc-message.success, .hc-message-success {
    border: solid 1px #8eb021;
  }
  .hc-message.warning, .hc-message-warning {
    border: solid 1px #f6c342;
  }
  .hc-message.error:after, .hc-message-error:after {
    color: #ffffff;
  }
  .hc-message.error:before, .hc-message-error:before {
    background-color: #d04437;
  }
  .hc-message.hint:after, .hc-message-hint:after, .hc-message.info:after, .hc-message-info:after {
    color: #ffffff;
  }

  .hc-message.hint:before, .hc-message-hint:before, .hc-message.info:before, .hc-message-info:before {
    background-color: #3572b0;
  }
  .hc-message.success:after, .hc-message-success:after {
    content: "\f104";
    color: #ffffff;
  }
  .hc-message.success:before, .hc-message-success:before {
    background-color: #8eb021;
  }
  .hc-message.warning:after, .hc-message-warning:after {
    color: #707070;
  }
  .hc-message.warning.waiting:after, .hc-message-warning.waiting:after {
    content: "";
    color: #707070;
  }
  .hc-message.warning:before, .hc-message-warning:before {
    background-color: #f6c342;
  }

  .hc-flags-container {
    position: fixed;
    top: 71px;
    right: 30px;
    z-index: 4000;

    .hc-flag {
      left: 0;
      max-height: 300px;
      opacity: 0;
      position: relative;
      top: -10px;
      transition: opacity 0.2s, top 0.5s;

      &[aria-hidden="false"] {
        opacity: 1;
        top: 0;
        left: 0;
      }

      &[aria-hidden="true"] {
        left: 300px;
        max-height: 0;
        opacity: 0;
        overflow: hidden;
        top: 0;
        transition: max-height 0.5s 0.5s, opacity 0.8s, left 1s;
      }

      .hc-message {
        .box-shadow(0 3px 6px rgba(0, 0, 0, 0.2));
        margin-bottom: 20px;
        position: relative;
        width: 300px;

        &.hc-message-video {
          padding-left: 30px;
          padding-right: 30px;
          width: 340px;

          &:before,
          &:after {
            display: none;
          }
        }

        &.closeable {
          .icon-close {
            cursor: pointer;
            left: auto;
            opacity: 0;
            position: absolute;
            right: 20px;
            top: 18px;
            background-image: none;
            color: #707070;
            text-indent: inherit;

            &:after {
              font-size: 10px;
              margin-top: 0;
              margin-left: 3px;
            }

            &:hover {
              color: #333;
            }
          }

          &:hover {
            .icon-close {
              opacity: 1;
            }
          }
        }

        .aui-nav-actions-list {
          font-size: 0;
          list-style: none;
          margin: 10px 0 0 0;
          padding: 0;

          > li {
            display: inline-block;
            font-size: 14px;
            margin-bottom: 5px;
            margin-right: 10px;
          }
        }

        .hc-message-body {
          margin-top: 10px;
        }
      }
    }
  }

  .hc-message-video {

    .sender-container {
      margin: 10px 0 20px;
      overflow: hidden;
    }
    .aui-avatar,
    .sender {
      float: left;
    }

    .sender {
      margin: 10px 0 0 10px;
      width: 280px;
      white-space: nowrap;
      overflow-x: hidden;
      text-overflow: ellipsis;
    }
  }
}