#hipchat, body {

  .hc-sidebar {
    background-color: @bgsidebar;
    height: 100%;
  }
  .hc-sidebar-scroll {
    overflow-x: hidden;
    overflow-y: auto;
  }
  .hc-sidebar-noscroll {
    overflow-x: hidden;
    overflow-y: hidden;
  }
  .hc-sidebar-footer {
    position: fixed;
    bottom: 0;
    border-top: 1px solid #cccccc;
    padding: 0;
    background-color: @bggray;
  }
  .hc-sidebar {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .hc-left-sidebar-col {
    box-sizing: border-box;
    position: relative;
    user-select: none;
    z-index: 1;
    .resize-handle {
      height: 100%;
      position: absolute;
      right: -5px;
      top: 0;
      cursor: col-resize;
      width: 10px;
      z-index: 3;
    }
    >nav {
      >div {
        width:inherit;
      }

    }
    .hc-unread-scroller {
      position:fixed;
      width:inherit;
      text-align:center;
      z-index:100;
      a {
        display:block;
        height: 15px;
        width: 28px;
        background-color: @mediumtext;
        margin:0 auto;
      }
      &.has-mention {
        a{
          background-color: @buttonblue;
        }
      }
      span {
        color:@white;
      }
    }
    .hc-unread-scroller-top {
      border-top: solid 3px @mediumtext;
      &.has-mention {
        border-color: @buttonblue;
      }
      a {
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
        span {
          margin-top:-4px;
        }
      }
    }
    .hc-unread-scroller-bottom {
      bottom:0;
      border-bottom: solid 3px @mediumtext;
      &.has-mention {
        border-color: @buttonblue;
      }
      a {
        border-top-right-radius: 5px;
        border-top-left-radius: 5px;

      }
    }
  }

  .hc-left-sidebar-col,
  .hc-left-sidebar-col .hc-sidebar-footer {
    width: @left-sidebar-width;
    border-right: 1px solid @bordergray;
    background-color: @bggray !important;
  }
  .aui-navgroup-inner {
    padding-top: 15px;
    .hc-sidebar-nav {
      .aui-nav-heading {
        border-top: none;
        cursor: default;
      }
    }
  }
  .hc-right-sidebar-col,
  .hc-right-sidebar-col .hc-sidebar-footer {
    border-left: 1px solid @bordergray;
    overflow: visible;
  }

  .hc-tab {
    position: relative;
    .room-name{
      display: inline-block;
      vertical-align: middle;
      max-width: @room-name-width;
      width: @room-name-width;
      word-wrap: normal;
      .ellipsis();
    }
    .aui-nav-item {
      padding: 9px 12px 10px 10px;
      max-height: 36px;
      box-sizing: border-box;
    }
    .hc-badge {
      background-color: @unreadBadgeColor;
      color: @unreadBadgeFontColor;
      font-weight: inherit;
      transition: opacity 0.2s;

      &.hc-mention {
        background-color: @unreadMentionColor;
      }
      &.hc-updating {
        opacity: 0.5;
      }
    }
    &.hc-has-badge {
      .aui-nav-item {
        padding-right: 41px;
      }
      a > span.room-name {
        font-weight: 500;
      }
    }
    &:hover {
      > a {
        background-color: @tabbghover;
      }
      .aui-nav-item {
        padding-right: 32px;
      }
      .hc-tab-close {
        border-color: transparent;
        background-color: transparent;
        display: block;
      }
      &.hc-has-badge {
        .hc-tab-close {
          display: none;
        }
      }
      &.hc-has-badge .aui-nav-item {
        padding-right: 41px;
      }
    }
    &.aui-nav-selected > a {
      background-color: @bgactive;
      font-weight: 400;
      color: #F5F5F5;

      span.icon-public, span.icon-private, span.icon-dot, span.icon-lobby, span.icon-close, span.icon-search {
        color: #F5F5F5;
      }
      span.icon-close {
        &:hover {
          color: #fff;
        }
      }
    }
  }
  .hc-tab-close {
    position: absolute;
    padding-left: 2px;
    padding-right: 2px;
    right: 10px;
    top: 4px;
    display: none;
    .hc-close-icon {
      &:hover {
        color: #333;
      }
      &:after {
        font-size: 9px;
      }
    }
  }
  .hc-tab, .hc-roster {
    > a {
      color: @text;
      .ellipsis();
    }
    .aui-icon {
      color: @icon;
    }
    .aui-nav-item {
      .aui-icon, .aui-avatar {
        margin-right: 8px;
      }
    }
    .aui-badge {
      position: absolute;
      right: 10px;
      margin-top: 1px;
    }
  }
  .hc-add-item-link {
    > a, > a:hover, span.aui-icon {
      color: @buttonblue
    }
  }

}

.hc-nav-panel {
  .nav-panel-content {
    &[aria-hidden="true"] {
      display: none;
    }

    &[aria-hidden="false"] {
      display: block;
    }

    h2 {
      .aui-lozenge {
        margin-left: 10px;
        position: relative;
        top: -2px;
      }
    }
  }

  .aui-nav li > button {
    color: #3572b0;
    display: block;
    line-height: 1.14285714285714;
    padding: 7px 10px;
    word-wrap: break-word;
    width: 100%;
    text-align: left;
    border-radius: 0;

    &:hover {
      background: #e6e6e6;
      text-decoration: none;
    }
  }
}

/* We need this level of specificity to override 'vertical-align' */
#hipchat {
  .hc-room, .hc-person{
    .aui-icon {
      vertical-align: middle;
    }
  }
}
