@import "mixins/flexbox";
@import "prettify";

@messageStatusTransitionOpacity: opacity 150ms ease-in-out;
@messageStatusTransitionBackgroundColor: background-color 150ms ease-in-out;

/* Chat Window CSS */
.hc-chat-panel, .fake-chat-panel {
  .flex-display();
  .flex(1);
  position: relative;

  .hc-chat-scrollbox {
    background-color: @white !important;
    overflow-y: auto;
    overflow-x: hidden;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translate3d(0, 0, 0);
  }
}

.prerender-panel {
  visibility: hidden;
  width: 100%;
  position: absolute;
  left: -999999px;
}
.hc-chat-spinner{
  padding: 25px 0;
}
.hc-chat-spinner{
  padding: 25px 0;
  .spinner {
    margin: 0 auto;
  }
}

.spinwrap{
  text-align: center;
  height: 100%;
  &:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -0.25em;
  }
  .hc-loading-spinner,
  .hc-loading-logo {
    display: inline-block;
    vertical-align: middle;
  }
}

.spinner-msg-wrap {
  text-align: center;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  &:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -0.25em;
  }
  .app-loading-message{
    font-weight: 500;
    position: relative;
    top: 50px;
  }
}
.hc-chat-row {
  display: table;
  table-layout: fixed;
  width: 100%;
  padding: 8px 0px;
  border-top: 1px solid @divider;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  transition: @messageStatusTransitionBackgroundColor;
  &:first-of-type {
    border-top: solid #fff 1px;
  }
  &:first-child {
    border-top: none;
  }
  .msg-status {
    transition: @messageStatusTransitionOpacity;
  }
  .msg-unconfirmed,
  .msg-failed,
  .msg-flaky {
    opacity: 0.6;
  }
  .msg-confirmed {
    opacity: 1;
    position: relative;
  }
  div {
    font-size:14px;
    line-height: 20px;
    .atlascard {
      margin-bottom: 5px;
    }
    &:last-of-type {
      > .atlascard {
        margin-bottom: 0;
      }
    }
    span.description {
      display: block;
      margin-top: 5px;
      clear: both;
    }
    .toggle-wrap {
      position: absolute;
      top: -5px;
      right: 5px;
      cursor: pointer;
      padding: 0 10px;
      margin:0;
      span.toggle-image {
        display: inline-block;
        height: 4px;
        width: 7px;
        background-size: cover;
      }
    }
    .toggle-attached-container {
      cursor: pointer;
      float: right;
      line-height: 1.4285;
      padding: 0 10px;
      margin-right: 5px;
    }
    .toggle-attached {
      display: inline-block;
      height: 4px;
      text-indent: -9999px;
      vertical-align: middle;
      width: 7px;
      background-size: cover;
    }
    .file-meta{
      display: inline-block;
      padding: 0 25px 0 0;
      vertical-align: top;
      a, span {
        margin-right: 5px;
      }
      .compact {
        a, span {
          display: inline;
        }
      }
      &.with-preview {
        span {
          color: #707070;
        }
        a, span {
          display: block;
        }
        &.compact {
          a {
            margin-right: 10px;
          }
          a, span {
            display: inline;
          }
        }
      }
    }
    .image-preview-wrapper {
      display: table-cell;
    }
    .file-preview-wrapper {
      display: inline-block;

      &.info {
        margin-right: 10px;
      }
    }
    .image-preview-wrapper, .file-preview-wrapper {
      vertical-align: middle;
      padding: 0;
      margin: 0 5px 0 0;
      .loading {
        border: dashed 2px @mediumtext;
        border-radius: 5px;
        height: 200px;
        width: 200px;
        vertical-align: middle;
        display: table-cell;
        text-align: center;
        color: @mediumtext;
        font-size: 14px;
        box-sizing: border-box;
      }
      a {
        display: inline-block;
      }
      img, video {
        width: auto;
        margin: 5px 0;
        vertical-align: middle;
        display: inline-block;
        max-height: 300px;
        max-width: 300px;
        border-radius: 3px;
        box-sizing: border-box;
      }
    }
  }
}
.hc-chat-from {
  display: table-cell;
  max-width: 110px;
  text-align: right;
  vertical-align: top;
  padding: 0px 10px;
  width: 110px;
  white-space: nowrap;
  overflow: hidden;
  .sender-name {
    font-weight: 500;
    color: @adgMediumGray;
    vertical-align: top;
    left: 50px;
    text-overflow: ellipsis;
    width: 92px;
    overflow-x: hidden;
    &:hover {
      color: @text;
    }
  }
  .ellipsis();
  &:hover {
    color: @text;
  }
  > span {
    cursor: pointer;
  }
}

.hc-chat-msg {
  display: table-cell;
  width: 100%;
  padding:0 10px;
  line-height: 20px;
  max-width: 0px;
  &.image-link {
    .toggle-wrap {
      top: 0;
      padding: 0 10px;
    }
  }
  img {
    vertical-align: middle;
      &:not(.remoticon) {
        margin-right: 5px;
      }
  }
  .hc-mention-user, .atTag {
    display: inline-block;
    background: @bggray;
    padding: 2px 5px;
    border: 1px solid @bordergray;
    border-radius: 3px;
    line-height: 16px;
    white-space: nowrap;
    a {
      color: @text;
      &:hover {
        text-decoration: none;
      }
    }
  }
  .hc-mention-me, .atTagMe {
    background: @bgblue;
    border: 1px solid @bgblue;
    color: #fff;
  }
  .hc-mention-me:before, .atTagMe:before {
    color: #fff;
  }
  .highlight {
    background-color: #fbecc5;
    border: solid 1px #f6c342;
    border-radius: 3px;
    padding: 0 2px;
  }
  pre {
    white-space: pre-wrap;
    div {
      word-break: break-all;
      margin:0;
    }
  }
  .icon-warning:after {
    content: "\f1b3";
    font-family: "Atlassian Icons";
    font-size: 16px;
    margin-right: 10px;
    .font-smoothing();
    font-style: normal;
    font-weight: normal;
  }
  .hexPreview {
    width: 18px;
    display: inline-block;
    text-decoration: none;
    border: 1px solid #999;
    border-radius: 3px;
    height: 18px;
  }
  .msg-line {
    &.deleted {
      font-style: italic;
      color: @adgMediumGray;

      span {
        padding-left: 3px;
      }
    }
    a.linkImage {
      float:left;
    }
    p {
      margin:0;
    }
    img.msg-link-favicon {
      max-height: 32px;
      max-width: 32px;
    }
  }
}

.message-confirm-state-enter {
  opacity: 0.01;
}

.message-confirm-state-enter.message-confirm-state-enter-active {
  opacity: 1;
  transition: @messageStatusTransitionOpacity;
}

.message-confirm-state-leave {
  opacity: 1;
}

.message-confirm-state-leave.message-confirm-state-leave-active {
  opacity: 0.01;
  transition: @messageStatusTransitionOpacity;
}

.hc-chat-status-unconfirmed,
.hc-chat-status-failed,
.hc-chat-status-time,
.hc-chat-status-edited {
  font-size: 11px;
  text-align: right;
  vertical-align: top;
  white-space: nowrap;
}
.hc-chat-status-edited {
  font-style: italic;
}
.hc-chat-status-unconfirmed,
.hc-chat-status-time,
.hc-chat-status-edited {
  float: right;
  margin-left: 10px;
}
.hc-chat-status-edited {
  margin-right: 25px;
}
.hc-chat-status-unconfirmed .unconfirmed-messaging {
  color: @failedUnconfirmedText;
  cursor: default;
}
.hc-chat-status-failed {
  .failed-messaging {
    color: @failedMessaging;
    cursor: default;
    font-weight: 500;
    margin-right: 3px;
  }
  .failed-message-actions > a {
    color: @failedMessageActions;
    margin-left: 7px;
  }
}
.hc-chat-time, .hc-chat-edited {
  color: @messageTime;
}
.hc-chat-row.hc-chat-row-failed,
.hc-chat-row.hc-chat-row-unconfirmed {
  .sender-name {
    color: @failedUnconfirmedSender;
  }
  .separator {
    color: @failedUnconfirmedText;
  }
}
.hc-chat-row-failed {
  background-color: @failedMessageBackground;
  border-top: 1px solid #fff;
}

.hc-chat-history {
  max-height: calc(~'100% - 189px');
  overflow-x: hidden;
  overflow-y: auto;
}
.hc-composing-message {
  width: 100%;
  color: @icon;
  white-space: nowrap;
  overflow: hidden;
  padding: 8px 0px 8px 140px;
  display: inline-block;
  &.hc-classic-neue {
    padding: 8px 0px 8px 55px;
  }
}

.hc-classic-neue {
  .hc-chat-from {
    display: table-cell;
    max-width: 25px;
    overflow: hidden;
    padding: 3px 10px;
    text-align: left;
    vertical-align: top;
    white-space: nowrap;
    width: 25px;
    .ellipsis();
    text-overflow: clip;

    &.no-action {
      cursor: inherit;
    }
    &:hover {
      color: @text;
    }

  }
  .hc-chat-status-unconfirmed,
  .hc-chat-status-failed,
  .hc-chat-status-time,
  .hc-chat-status-edited {
    position: absolute;
    right: auto;
    margin: 0;
    margin-right: 0px;
    text-align: left;
    vertical-align: baseline;
    float: none;
    display: inline-block;
  }
  .hc-chat-history {
    max-height: calc(~'100% - 189px');
    overflow-x: hidden;
    overflow-y: auto;
  }
  .hc-chat-msg {
    width: 100%;
    img {
      vertical-align: middle;
      &:not(.remoticon) {
        margin-right: 5px;
      }
    }
  }
  .sender-name {
    &:hover {
      color: @text;
    }
    vertical-align: top;
    color: @adgMediumGray;
    cursor: pointer;
    font-weight: 500;
  }
  .separator {
    color: @adgMediumGray;
    padding: 0 5px;
  }
}
.hc-col {
  float: left;
}

.unread-line {
  margin: 0 0 4px -140px;
  width: 100%;
  height: 3px;
  hr {
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #3572b0;
    border: 0;
    margin: 0 0 4px 0;
  }
}

.hc-classic-neue .unread-line {
  margin-left: -55px;
}

.hc-chat-msg div:last-of-type.unread-line {
  margin-bottom: -8px;
  margin-top: 7px;
  hr {
    margin: 0;
  }
}

.hc-chat-msg {
  .info {
    color: @icon;
  }
  .msg-fail-container {
    display: block;
    margin: 0;

    .msg-line {
      color: @icon;
      display: inline-block;
      padding-left: 8px;
    }
  }
  .msg-status {
    .msg-line {
      display: block;
      margin: 0 0 5px 0;
      word-wrap: break-word;
      .info {
        color: @icon;
      }
      &.code,
      &.monospace {
        display: inline;
        width: 100%;
      }
      a > img {
        margin: 5px 10px 5px 0;
        display: inline-block;
      }
      &.notification {
        img {
          max-width: 100%;
        }
      }
      &.truncatable {
        display: inline-block;
        width: calc(~'100% - 100px');
        overflow-y: hidden;
        position: relative;
        padding: 0;
        border-radius: 3px;
        vertical-align: top;
        .truncate-wrap {
          display: block;
          .msg-wrap {
            white-space: pre-wrap;
            display: inherit;
            overflow-x: hidden;
            blockquote{
              padding: 0 20px;
              margin-left: 19px;
            }
          }
        }
        &.truncated {
          max-height: 140px;
          .truncate-wrap {
            max-height: 100px;
            display: block;
            overflow-y: hidden;
            margin: 0;
          }
          .pre-wrap {
            height: 120px;
            margin-right: 0;
          }
        }
        &.expanded {
          .pre-wrap {
            margin-right: 0;
          }
        }
        &.code span.truncatable, &.code div.truncatable,
        &.monospace span.truncatable, &.monospace div.truncatable{
          border-top: dashed 1px @bordergray;
          margin:0;
        }
        span.truncatable, div.truncatable {
          position: relative;
          display: block;
          bottom: 0;
          left: 0;
          padding: 5px 0 0;
          width: 100%;
          box-sizing: border-box;
          border-top: solid 1px @white;
          margin:5px 0 0 0;
          a {
            color: @lightblue;
            font-size:.9em;
          }
          &.expanded {
            top: -7px;
            border-top: none;
          }
          &.code,
          &.monospace {
            background-color: @white;
            border: solid 1px @adgAshGray;
            border-top: none;
          }
        }
      }
      div.pre-wrap {
        display: block;
        border: 1px solid @adgAshGray;
        border-radius: 3px;
        margin-right: 100px;
      }
    }
    &:last-of-type{
      .msg-line {
        margin: 0;
      }
    }
  }

  pre {
    div {
      word-wrap: break-word;
      max-width: 100%;
      display: inline-block;
    }
  }
}
.hc-msg-nocolor {
  .hc-chat-msg {
    .msg-line {
      span.truncatable {
        border-top: solid 1px #e9e9e9;
      }
    }
  }
}
.hc-msg-gray {
  background: @bggray;
  border-top: solid #fff 1px;
  &:before {
    content: '';
    height: 100%;
    width: 4px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: @darkerborder;
  }
}
.hc-msg-yellow {
  background: @bgyellow;
  border-top: solid #fff 1px;
  &:before {
    content: '';
    height: 100%;
    width: 4px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: @yellow;
  }
}
.hc-msg-red {
  background: @bgred;
  border-top: solid #fff 1px;
  &:before {
    content: '';
    height: 100%;
    width: 4px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: @red;
  }
}
.hc-msg-green {
  background: @bggreen;
  border-top: solid #fff 1px;
  &:before {
    content: '';
    height: 100%;
    width: 4px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: @green;
  }
}
.hc-msg-purple {
  background: @bgpurple;
  border-top: solid #fff 1px;
  &:before {
    content: '';
    height: 100%;
    width: 4px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: @purple;
  }
}
.hc-msg-blue {
  background: @msgblue;
  border-top: solid #fff 1px;
  .sender-name {
    color: @currentUserName;
  }
}

.hc-msg-blue + .hc-msg-nocolor, .hc-msg-purple + .hc-msg-nocolor, .hc-msg-green + .hc-msg-nocolor, .hc-msg-red + .hc-msg-nocolor, .hc-msg-yellow + .hc-msg-nocolor, .hc-msg-gray + .hc-msg-nocolor{
  border-top: solid #fff 1px;
}

.hc-msg-video,
.hc-msg-twitter_status,
.hc-msg-twitter_user,
.hc-msg-link,
.hc-msg-image {
  background: @bggray;
  border-top: solid #fff 1px;
  &:before {
    content: '';
    height: 100%;
    width: 4px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: transparent;
  }
  .hc-chat-msg{
    .msg-line {
      div.link-wrap,
      div.video-wrap {
        margin: 0;
        width: 100%;
        p {
          margin: 0;
        }
        a.linkImage {
          float:left;
        }
        a {
          &:hover {
            text-decoration: none;
          }
        }
        iframe {
          float: left;
          margin: 0 10px 3px 0;
          border: none;
          display: inline-block;
        }
        p.linkTitle {
          font-weight: 700;

          a {
            color: @text;
          }
        }
      }
    }
  }
}

.video-wrap .file-video-default-poster {
  display:inline-block;
  background:black;
  cursor: pointer;
  width: 300px;
  height: 169px;
  position: relative;
  margin: 5px 5px 5px 0;
  float: left;

  &:after {
    content: "";
    position: absolute;
    display: block;
    background: url(data:image/svg+xml;charset=utf-8;base64,) 50% 50% no-repeat;
    background-size: 24px;
    background-color: #000;
    background-color: rgba(0,0,0,0.5);
    width: 50px;
    height: 50px;
    top: 50%;
    left: 50%;
    border-radius: 6px;
    margin-left: -25px;
    margin-top: -25px;
  }

  img {
    position: absolute;
    left: 50%;
    top: 50%;
    width: inherit;
    height: inherit;
    margin: -85px 0 0 -150px !important;
  }
}

.date-block{
  display: inline-block;
  width: 100%;
}
.date-divider{
  display: block;
  text-align: center;
  color: @subdued;
  font-size: 14px;
  margin: 20px 0;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    border-bottom: 1px solid @divider;
  }
  span {
    background-color: #fff;
    padding: 0 10px;
    position: relative;
    font-size: 16px;
    color: @text;
    cursor: default;
  }
}
.clear {
  clear: both;
  height: 0;
}
.date-divider + .hc-chat-row,
.unread-line-row + .hc-chat-row {
  border-top: none;
}

.empty {
  width: 100%;
  position: absolute;
  bottom: 15px;
  text-align: center;
  div {
    display: inline-block;
    vertical-align: top;
  }
  /* HW-33 Restructure the lobby no results state to look like the old New Chat no results state. */
  &.empty-search-lobby {
    .empty-state {
      .empty-state-msg {
        display: block;
        text-align: center;
        padding-left: 0px;
        padding-top: 15px;
      }
    }
  }
  .empty-state {
    height: 110px;
    margin: 0 auto;
    &.chat {
      .empty-state-img{
        height: 104px;
        width: 110px;
      }
    }
    &.groupchat {
      .empty-state-img{
        height: 89px;
        width: 104px;
      }
    }
    .empty-state-msg {
      text-align: left;
      padding-left: 15px;
      vertical-align: middle;
      line-height: 22px;
      padding-top: 8px;
      .empty-header {
        cursor: default;
        font-weight: bold;
        text-transform: uppercase;
        font-size: 14px;
      }
      .empty-msg {
        img {
          position: absolute;
        }
      }
    }
  }
}

.twitter-tweet.twitter-tweet-rendered {
  margin:0 !important;
}

div.hc-msg-file, div.image-link {
  div.actionable-msg-container {
    .hc-dropdown {
      margin-right: 30px;
    }
  }
}

div.actionable-msg-container {

  display: table;

  .hc-dropdown {
    vertical-align: middle;
    height: 100%;
    float: right;
    padding-left: 2px;

    .aui-button {
      height: 20px;
      padding: 0 5px;
      opacity: 0;
    }
  }

  .hc-chat-time, .hc-chat-edited {
    margin-right: -31px;
  }

  >.msg-line, .hc-dropdown {
    display:table-cell;
  }

  &.hc-message-actions-active {
    .hc-chat-time, .hc-chat-edited {
      visibility:hidden;
    }

    .hc-dropdown .aui-button {
      opacity: 1;

      span {
        color: @subdued;
      }

      &.msg-actions-above {
        border-radius: 0 0 3px 3px;
        border-top: 0;
        padding-top: 1px;
        position: relative;
        z-index: 2;

        &:hover {
          border-bottom: solid thin @darkerborder;
        }
      }
    }
  }

  &:hover {
    .hc-dropdown {
      .aui-button {
        opacity: 1;

        span {
          color: @icon;
        }

        &:hover {
          span {
            color: @subdued;
          }
        }
      }
    }

    .hc-chat-time, .hc-chat-edited {
      visibility:hidden;
    }
  }
}


.msg-action-dropdown {
  max-width: 200px;
  width: 200px;

  &.msg-actions-above {
    border-radius: 3px 3px 0 0;
  }
}

button.aui-button.aui-button-subtle.aui-dropdown2-trigger.aui-dropdown2-trigger-arrowless.hc-dropdown-trigger.hc-dropdown-above {
  border-radius: 0 0 3px 3px;
  border-top: 0;
  padding-top: 1px;
  position: relative;
  z-index: 3;

  &:hover {
    border-radius: 0 0 3px 3px !important;
    border-bottom: solid thin @darkerborder;
  }
}

div.aui-style-default.aui-dropdown2.hc-dropdown-container.hc-dropdown-above {
  border-radius: 3px 3px 0 0;
}
