@import "status_menu";
@import "guest_access_information";

#hipchat, body {

  .hc-chat-input-autocomplete {
    .aui-inline-dialog {
      bottom:30px;
      cursor: pointer;
      .aui-inline-dialog-contents {
        max-height:190px;
        min-width:260px;
        max-width:300px;
        padding:0;
        overflow:auto;
        ul {
          margin:0;
          padding:0;
          li {
            list-style-type:none;
            border-left: solid 3px transparent;
            padding:0 5px 0 4px;
            overflow:hidden;
            text-overflow: ellipsis;
            color: @mediumtext;
            .hc-ac-avatar{
              vertical-align: middle;
              display:inline-block;
              margin-left: 3px;
              .aui-avatar{
                .mention-group-icon {
                  width: 24px;
                  height: 24px;
                  border-radius: 100%;
                  opacity: 0.8;
                  background-size: cover;
                }
                .aui-avatar-inner {
                  background-size: cover;
                  background-position: center;
                  opacity: 0.8;
                  border-radius: 100%;
                }
              }
            }
            .hc-ac-preview {
              height: 20px;
              width: 20px;
              img {
                max-height: 100%;
                max-width: 100%;
              }
              display:inline-block;
              text-align: center;
              color: @icon;
              vertical-align: middle;
            }
            .hc-ac-name {
              margin-left:10px;
              line-height:35px;
              color:@text;
            }
            .hc-ac-mention-name {
              color: @mediumtext;
              margin-left:10px;
              font-size:.9em;
            }
            .hc-ac-row {
              overflow:hidden;
              text-overflow:ellipsis;
              white-space: nowrap;
            }
          }
          li.hc-autocomplete-item-selected {
            background-color: @selectedbg;
            border-left-color: @selectedListitem;
            .aui-avatar {
              .mention-group-icon, .aui-avatar-inner {
                opacity: 1;
              }
            }
          }
        }
      }
    }
    .aui-inline-dialog .aui-inline-dialog-arrow,
    .aui-inline-dialog .arrow {
      top: auto;
      left: 13%;
    }
    .aui-inline-dialog .arrow.right{
      left: auto;
      right: 13%;
    }
    &.hc-slash-command-ac {
      .aui-inline-dialog, .aui-inline-dialog-contents {
        ul li {
          .hc-ac-name-container {
            float: left;
            width: 100%;
            padding-top: 5px;
          }
          .hc-ac-name {
            float: left;
            padding-right: 5px;
            line-height: 20px;
            display: inline-block;
          }
          .hc-ac-usage{
            float: left;
            line-height: 20px;
            display: inline-block;
            white-space: nowrap;
            color: #8C8C8C;
          }
          .hc-ac-description {
            float: left;
            margin-left: 10px;
            line-height: 20px;
            padding-bottom: 5px;
            color: #B3B3B3;
            font-size: .95em;
          }
        }
      }
    }
  }
  
}

.aui-inline-dialog {
  &.dialog-callout {
    .aui-inline-dialog-contents {
      border: solid 3px @blueBorder;
      .box-shadow(none);
    }
    .arrow, .aui-inline-dialog-arrow {
      top: -6px;
    }
    .aui-inline-dialog-arrow {
      &.aui-css-arrow {
        &:before {
          border-style: solid;
          border-width: 0 12px 12px;
          border-color: @blueBorder transparent;
          left: -12px;
          top: -3px;
        }
      }
    }
  }
}

.aui-inline-dialog.show {
  display: block;
}