.hc-default-avatar {
  border-radius: 100%;
  text-align: center;
  color: #fff;
  background-size: cover;
  background-position: 50% 50%;
}

.aui-avatar {
  img {
    border-radius: 100%;
  }
}

.aui-avatar-xsmall {
  .hc-default-avatar {
    .avatar-helper(11px, 16px);
  }
  .aui-avatar-img {
    .avatar-image-helper(16px, @white);
  }
}

.aui-avatar-small {
  .hc-default-avatar{
    .avatar-helper(10px, 24px);
  }
  .aui-avatar-img {
    .avatar-image-helper(24px, @white);
  }
}

.aui-avatar-medium {
  .hc-default-avatar{
    .avatar-helper(14px, 32px);
  }
  .aui-avatar-img {
    .avatar-image-helper(32px, @white);
  }
}

.aui-avatar-large {
  .hc-default-avatar{
    .avatar-helper(32px, 48px);
  }
  .aui-avatar-img {
    .avatar-image-helper(48px, @white);
  }
}

.aui-avatar-xlarge {
  .hc-default-avatar{
    .avatar-helper(48px, 64px);
  }
  .aui-avatar-img {
    .avatar-image-helper(64px, @white);
  }
}

.aui-avatar-xxlarge {
  .hc-default-avatar{
    .avatar-helper(64px, 96px);
  }
  .aui-avatar-img {
    .avatar-image-helper(96px, @white);
  }
}
//Specific background colors was added in order to fix issue with broken Gif avatars in Chrome Version 53.
//The fix is to give Gif avatars containers a background color.
nav {
  .aui-avatar-small {
    .aui-avatar-img {
      .avatar-image-helper(24px, @defaultAvatarColor);
    }
  }
  .aui-dropdown2-active, a:hover {
    .aui-avatar-small {
      .aui-avatar-img {
        .avatar-image-helper(24px, @selectedListitem);
      }
    }
  } 
}
.hc-right-sidebar-col .aui-nav-selected {
  .aui-avatar-small {
    .aui-avatar-img {
      .avatar-image-helper(24px, @bgactive);
    }
  }
  .aui-avatar-xsmall {
    .aui-avatar-img {
      .avatar-image-helper(16px, @bgactive);
    }
  }
}
.hc-main-col header {
  .aui-avatar-large {
    .aui-avatar-img {
      .avatar-image-helper(48px, @bggray);
    }
  }
}
.hc-msg-blue {
  .aui-avatar-medium {
    .aui-avatar-img {
      .avatar-image-helper(32px, @msgblue);
    }
  }
}
.hc-autocomplete-item-selected, .hc-lobby-list-item.selected {
  .aui-avatar-small {
    .aui-avatar-img {
      .avatar-image-helper(24px, @selectedbg);
    }
  }
}

.aui-avatar-project {
  background-color: transparent;
  box-shadow: none;
  &:before {
    border: none;
  }
}

.aui-nav-selected {
  .aui-avatar-project {
    box-shadow: none;
    &:before {
      border: 1px solid @white;
    }
  }
}

// The selectors I'm having to override here have ridiculous specificity
#hipchat #page {
  .aui-avatar svg.aui-icon {
    bottom: -3px;
    height: 14px;
    line-height: 1;
    margin: auto;
    position: absolute;
    right: -4px;
    top: auto;
    width: 14px;
  }
  .aui-avatar-xsmall svg.aui-icon {
    bottom: -4px;
    height: 11px;
    right: -5px;
    width: 11px;
  }
}
