@tipsify-background-color: rgba(51, 51, 51, .9);
@tipsify-arrow-size: 3px;

#tipsify {

  .tipsify-content {
    position: absolute;
    box-sizing: border-box;

    text-align: center;

    opacity: 0;
    animation: tipsify_appear 100ms 1;
    animation-delay: 100ms;
    animation-fill-mode: forwards;
    transition: transform 100ms ease-in-out;

    // Arrow
    &::before {
      content: '';
      position: absolute;
      display: inline-block;

      border: @tipsify-arrow-size solid transparent;
      border-top: 0;
      border-bottom-color: @tipsify-background-color;

      width: 0;
      height: 0;
      left: calc(50% - @tipsify-arrow-size/2);
      top: -@tipsify-arrow-size;
    }

    &.reversed {
      &::before {
        top: auto;
        bottom: -@tipsify-arrow-size;

        border: @tipsify-arrow-size solid transparent;
        border-top-color: @tipsify-background-color;
        border-bottom: 0;
      }
    }

    .tipsify-content-inner {
      box-sizing: border-box;
      display: inline-block;
      padding: 5px 8px 4px 8px;

      background: @tipsify-background-color;
      color: @white;
      border-radius: 3px;
      font-size: 12px;
      text-align: center;
      white-space: nowrap;
    }
  }
}


@keyframes tipsify_appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}