#hipchat, body {
  @font-face {
    font-family: "HipChat Icons";
    src: url("../assets/fonts/hipchat.eot?r=@{RELEASE_DATE}");
    src: url("../assets/fonts/hipchat.eot?r=@{RELEASE_DATE}#iefix") format("embedded-opentype"), url("../assets/fonts/hipchat.woff?r=@{RELEASE_DATE}") format("woff"), url("../assets/fonts/hipchat.ttf?r=@{RELEASE_DATE}") format("truetype"), url("../assets/fonts/hipchat.svg#hipchat?r=@{RELEASE_DATE}") format("svg");
    font-weight: 400;
    font-style: normal
  }

  .hipchat-icon-large, .hipchat-icon-medium, .hipchat-icon-small, .hipchat-icon-xsmall {
    line-height: 0;
    position: relative;
    vertical-align: text-top
  }

  .hipchat-icon-xsmall {
    height: 13px;
    width: 13px
  }

  .hipchat-icon-small {
    height: 16px;
    width: 16px
  }

  .hipchat-icon-medium {
    height: 24px;
    width: 24px
  }

  .hipchat-icon-large {
    height: 32px;
    width: 32px
  }

  .hipchat-icon-huge {
    height: 48px;
    width: 48px
  }

  .hipchat-icon-large:after, .hipchat-icon-xsmall:after, .hipchat-icon-small:after, .hipchat-icon-medium:before, .hipchat-icon-medium:after, .hipchat-icon-large:before, .hipchat-icon-huge:before, .hipchat-icon-huge:after, .hipchat-icon-small:before, .hipchat-icon-xsmall:before {
    color: inherit;
    font-family: "HipChat Icons";
    font-weight: 400;
    .font-smoothing();
    font-style: normal;
    left: 0;
    line-height: 1;
    position: absolute;
    text-indent: 0;
    speak: none;
    top: 50%
  }

  .hipchat-icon-xsmall:after {
    font-size: 13px;
    margin-top: -6px
  }

  .hipchat-icon-small:after {
    font-size: 16px;
    margin-top: -8px
  }

  .hipchat-icon-medium:after {
    font-size: 24px;
    margin-top: -12px
  }

  .hipchat-icon-large:after {
    font-size: 32px;
    margin-top: -16px
  }

  .hipchat-icon-large:after {
    font-size: 48px;
    margin-top: -24px
  }

  .icon-attachment:after {
    content: "\e618";
  }
  .icon-close:after {
    content: "\e619";
  }
  .icon-emoticon:after {
    content: "\e61a";
  }
  .icon-search:after {
    content: "\e61b";
  }
  .icon-audio-no-slash:after {
    content: "\e600";
  }
  .icon-mention:after,
  .icon-unread:after,
  .icon-file:after {
    content: "\e605";
  }
  .icon-full-screen:after {
    content: "\e606";
  }
  .icon-link:after {
    content: "\e607";
  }
  .icon-lobby:after {
    content: "\e608";
  }
  .icon-lock:after {
    content: "\e609";
  }
  .icon-phone-hang-up:after {
    content: "\e60b";
  }
  .icon-phone:after {
    content: "\e60c";
  }
  .icon-private:after, .icon-secret:after {
    content: "\e60d";
  }
  .icon-public:after {
    content: "\e60e";
  }
  .icon-settings:after {
    content: "\e60f";
  }
  .icon-share:after {
    content: "\e610";
  }
  .icon-signout:after {
    content: "\e611";
  }
  .icon-status:after {
    content: "\e612";
  }
  .icon-turn-camera:after {
    content: "\e613";
  }
  .icon-user:after {
    content: "\e615";
  }
  .icon-camera-no-slash:after {
    content: "\e616";
  }
  .icon-camera:after {
    content: "\e617";
  }
  .icon-volume-loud:after {
    content: "\e61d";
  }
  .icon-volume-normal:after {
    content: "\e61e";
  }
  .icon-volume-quiet:after {
    content: "\e61f";
  }
  .icon-integrations:after {
    content: "\e620";
  }
}
