@service_selector_bg: '../assets/svgs/native/service_selector_background.svg';
@service_selector_server: '../assets/svgs/native/service_selector_server.svg';
@service_selector_cloud: '../assets/svgs/native/service_selector_cloud.svg';
@service_login_form_height: 50px;

body.hc-service-selector {
  min-width: 500px;
}

#hipchat-service-selector {

  .hc-service-server-img,
  .hc-service-cloud-img {
    display:block;
    width: 120px;
    height: 120px;
  }

  .hc-service-server-img {
    background-image: url(@service_selector_server);
  }

  .hc-service-cloud-img {
    background-image: url(@service_selector_cloud);
  }

  .aui-group {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-image: url(@service_selector_bg);
    background-size: 50%;
    background-position: -100px -8px;
    width: 100%;
    height: 100%;
  }

  .aui-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    padding: 0;

    &.hc-service-selector-dark {
      background-color: rgba(132, 187, 198, 0.1);
    }

    &.hc-service-server-login {
      position: relative;
      top: -@service_login_form_height;
    }

    .aui-button {
      margin-bottom: 10px;
    }
  }

  h2 {
    color: @adgNavy;
    margin-top: 9px;
  }

  p {
    color: @adgMediumGray;
    font-size: 16px;
    margin: 5px 0 15px;
  }

  form {
    margin-top: 0;
    width: 275px;
    max-height: @service_login_form_height;
    min-height: @service_login_form_height;

    .aui-button {
      margin-top: 20px;
    }
  }

  input {
    &.long-field {
      width: 100%;
    }
  }

  .cancel-wrap {
    display: inline-block;

    .hc-spinner {
      position: relative;
      top: -4px;
      left: 20px;
    }
  }

  .error-message-container {
    margin-top: 10px;
    height: 55px;
  }

  .error-message {
    font-size: 13px;
    color: @adgRed;
  }

  .cancel-button {
    margin-left: 10px;
  }
}
