#hipchat, body {

  .app-header{
    min-height: @app-header-height;
    position: relative;
    user-select: none;
    &.banner-shown {
      min-height: @app-header-with-banner-height;
      animation: slidedown @banner-animation-speed;
    }
    &.banner-dismissed {
      min-height: @app-header-height;
      animation: slideup @banner-animation-speed;
    }
  }

  .hc-header-connection {
    display: flex;
    align-items: center;
    line-height: 1;
    padding: 0px 10px;
    height: @app-header-height;
    box-sizing: border-box;
    opacity: 1;
    transition: opacity 0.4s;

    a {
      color: inherit;
      display: inline;
      font-weight: bold;
      padding: 0 0 0 5px;
    }

    &.connected {
      opacity: 0;
    }
    &.disconnected {
      background: @red;

      a {
        &:hover {
          background: @red;
          text-decoration: underline;
        }
      }
    }
    &.reconnecting {
      padding-left: 24px;
    }
    &.countdown {
      width: 260px;
      justify-content: center;
      background: @adgYellow;
      color: @adgCharcoal;

      a {
        &:hover {
          background: @adgYellow;
          text-decoration: underline;
        }
      }
    }
    .connection-status {
      display: inline-block;
      margin-left: 8px;
    }
  }

  #connectionStatusMessage {
    right: 106px;
    width: 330px;

    .aui-inline-dialog-contents {
      padding: 15px;
    }

    &[aria-hidden='false'] {
      display: block;
    }

    .aui-css-arrow {
      right: 22px;
    }

    .retry-connection {
      color: @subdueddate;
    }

    p {
      &:first-child {
        margin-top: 0;
      }
      margin-top: 18px;
    }
  }

  .hc-banner-warning {
    color: #fff !important;
    background-color: #F79232;
    box-sizing: border-box;
    white-space: nowrap;
    position: absolute;
    bottom: @app-header-height;
    left: 0;
    right: 0;
    height: @app-header-banner-height;
    &:before{
      background-color: #F79232;
    }
  }
  .hc-banner-warning a {
    color: #fff !important;
    &.enable {
      text-decoration: underline !important;
      font-weight: 600;
    }
  }
  .aui-header {
    float: none;
    position: absolute;
    bottom: 0;
    width: 100%;
    min-width: 668px;
    height: @app-header-height;
    .aui-header-logo-hc .aui-header-logo-device {
      height: 24px;
      width: 97px;
      display: block;
      float: left;
      margin: 8px 0;
      padding: 0;
      text-indent: -9999px;
      text-align: left;
    }
    .aui-button {
      &.aui-button-primary {
        &.is-native {
          margin-left: 0;
        }
      }
    }
    .status-dropdown {
      span {
        vertical-align: top;
      }
      .aui-dropdown2-trigger {
        padding-right: 28px !important;
      }
      .aui-dropdown2-trigger:after {
        display: none;
      }
    }

  }

  .hc-connection-notification {
    position: fixed;
    top: 56px; // 41px (header height) + 15px
    right: 15px;
    opacity: 1;
    transition: opacity 200ms ease-in-out;
    a.inactive {
      color: #999;
      cursor: default;
      &:hover {
        text-decoration: none;
      }
    }
    .spin-wrap {
      display: inline-block;
      vertical-align: top;
      position: absolute;
      left: 20px;
      top: 50px;
    }
    span {
      display: inline-block;
      &.text {
        height: 60px;
      }
    }
  }

}