#hipchat {
  form {
    &.aui {
      .radio {
        &.notif-global-radio {
          padding-bottom: 5px;
        }
        .hipchat-icon-small {
          margin-right: 5px;
        }
      }
      .description {
        cursor: default;
      }
    }
  }
  form.aui div.checkbox input.checkbox,
  form.aui div.radio input.radio {
    height: auto;
    margin-top: 4px;
  }
}
#room-notifications-form {
  &.aui {
    .radio {
      .description {
        word-wrap: break-word;
      }
    }
  }
  fieldset.group {
    padding-left: 5px;

    .hipchat-icon-small {
      color: #707070;
      &.global {
        position: absolute;
        top: 7px;
        left: 78px;
      }
    }

    .global-level-label {
      position: absolute;
      top: 5px;
      left: 100px;
    }

    &.override-opts {
      padding-left: 25px;
    }
  }
}
