@import "../variables";
@import "../colors";
@import "../dark_colors";
@import "../mixins/mixins";
@import "../avatars";
@import "../mixins/color_schemes";

.rowColors() {
  .hc-msg-gray {
    background: @darkChatPanelBg;
    border-top-color: @lightBorder;
    opacity: 0.95;
    .hc-chat-from {
      color: @lightBorder;
    }
    &:before {
      background-color: @lightBorder;
    }
    &:after {
      background-color: @lightBorder;
      opacity: .4;
    }
  }

  .hc-msg-yellow {
    background: @darkChatPanelBg;
    border-top-color: @lightBorder;
    opacity: 0.95;
    .hc-chat-from {
      color: @darkYellow;
    }
    &:before {
      background-color: @darkYellow;
    }
    &:after {
      background-color: @darkYellow;
      opacity: .15;
    }
  }
  .hc-msg-red {
    background: @darkChatPanelBg;
    border-top-color: @lightBorder;
    opacity: 0.95;
    .hc-chat-from {
      color: @darkRed;
    }
    &:before {
      background-color: @darkRed;
    }
    &:after {
      background-color: @darkRed;
      opacity: .2;
    }
  }
  .hc-msg-green {
    background: @darkChatPanelBg;
    border-top-color: @lightBorder;
    opacity: 0.95;
    .hc-chat-from {
      color: @darkGreen;
    }
    &:before {
      background-color: @darkGreen;
    }
    &:after {
      background-color: @darkGreen;
      opacity: .2;
    }
  }
  .hc-msg-purple {
    background: @darkChatPanelBg;
    border-top-color: @lightBorder;
    opacity: 0.95;
    .hc-chat-from {
      color: @darkPurple;
    }
    &:before {
      background-color: @darkPurple;
    }
    &:after {
      background-color: @darkPurple;
      opacity: .2;
    }
  }
  .hc-msg-blue {
    background: @fromMe;
    border-top-color: @darkChatPanelBg;
    color: @darkGrayText;
    opacity: 0.95;
    .hc-chat-from {
      color: @textColor;
    }
    &:after {
      background-color: @fromMe;
      opacity: .2;
    }
  }
}

body {
  &.dark {
    #hipchat {

      .spinwrap {
        background-color: @darkHeader;
      }

      /**
      * General Components
      *
      */
      .hc-rooms-container {
        background-color: @black;
      }
      .hc-qs + .aui-blanket {
        &[aria-hidden="false"] {
          opacity: @blanket_opacity_less;
        }
      }

      /**
       * Lobby
       *
       */
      form.hc-filter-form {
        &::after {
          color: @darkGrayText;
        }
        input {
          background-color: @fromMe;
          color: @darkGrayText;
          border-color: @darkBorder;
        }
      }
      .hc-lobby-panel {
        background-color: @darkLobbyPanelBg;
        &.hc-lobby-panel-content .hc-lobby-list-item {
          background: @fromMe;
          border-bottom-color: @darkChatPanelBg;

          .hc-lobby-list-name {
            color: @darkGrayText;
          }

          &.selected{
            background-color: @darkHeader;
            .hc-lobby-list-name, .icon-public, .icon-private {
              color: white;
            }
          }
        }

        .empty-lobby {
          color: @darkGrayText;
          a {
            color: @linkColor;
          }
        }
      }

      /**
       * Left Sidebar
       *
       */
      .hc-left-sidebar-col {
        border-right: 2px solid @darkBorder;
        .hc-sidebar {
          background-color: @darkHeader;
          width: 100%;
          border-right: 1px solid #333940;
          z-index: 10;
          align-items: stretch;
          .aui-nav > li.aui-nav-heading {
            color: white;
            opacity: .6;
          }
          .hc-tab {
            opacity: .6;
          }
          .hc-tab.aui-nav-selected, .hc-tab:hover {
            opacity: 1;
          }
          .hc-tab > a {
            color: @darkGrayText;

            &.hc-tab-close:hover .hc-close-icon {
              color: @white;
            }
          }
          .hc-tab:hover > .aui-nav-item {
            background-color: @darkHighlight;
            color: white;
          }
          .hc-tab.aui-nav-selected > a {
            background-color: @darkSelected;
            color: white;
            &.hc-tab-close .hc-close-icon:hover {
              color: white;
            }
          }
          .hc-tab.aui-nav-selected:hover > a {
            background-color: @darkSelected;
            color: white;

          }
        }
        .hc-add-item-link > a.aui-nav-item:hover {
          background-color: @darkHighlight;
          color: white;
        }
        .hc-tab .aui-icon {
          color: @white;

          &.hc-close-icon {
            color: @darkGrayText;
          }
        }
        .hc-sidebar-footer {
          background-color: @darkHeader;
        }
      }

      /**
       * Right Sidebar
       *
       */
      .hc-right-sidebar-col {
        border-left-color: @darkBorder;


        /**
        * Integrations Sidebar
        */
        div.hc-integrations {
          background-color: @darkHeader;

          .hc-integration-view-sidebar {
            background-color: @fromMe;

            .hc-integration-sidebar-header {
              border-bottom: 1px solid @darkBorder;
            }
          }

          .hc-integrations-configure-button {
            border-color: @darkBorder;

            a {
              color: @darkGrayText;

              &:hover {
                color: @white;
              }
            }
          }

          .aui-iconfont-close-dialog {
            &:before {
              color: @darkGrayText;
            }
          }
          .aui-iconfont-close-dialog:hover {
            &:before {
              color: @white;
            }
          }

          .hc-glance {
            background-color: @fromMe;
            border-color: @darkBorder;
            color: @textColor;

            a, .aui-button-link {
              color: @linkColor;
            }

            &:hover {
              border-color: @buttonblue;
            }

            &.error {
              background-color: @darkHeader;
            }
          }
          .hc-integration-advertisement {
            background-color: transparent;
          }
        }

        .hc-tab-es {
          .hc-tab-es-title {
            color: @textColor;
          }

          .hc-tab-es-msg a {
            color: @linkColor;
          }
        }
      }

      /**
       * Chat Header
       *
       */
      .aui-page-header {
        background-color: @darkHeader;
        color: @darkGrayText;
        border-bottom-color: @darkBorder;
        h3 {
          color: white;
        }
        a {
          color: @linkColor;
        }
        .aui-button, .aui-button:visited, a.aui-button {
          border-color: @darkBorder;
          background: @darkHeader;
          color: @darkGrayText;
          .aui-icon {
            color: @darkGrayText;
          }
          &.active {
            box-shadow: inset 0 3px 6px 0 rgba(0, 0, 0, 1);
            background: @darkBorder;
          }
          &:hover, &.active {
            color: white;
            .aui-icon {
              color: white;
            }
          }
          &.aui-dropdown2-trigger:not(.aui-dropdown2-trigger-arrowless):after{
            border-top-color: @white;
          }
        }
      }

      .hc-header-connection {
        height: @app-header-dark-theme-height;
      }

      /**
       * Chat Panel
       *
       */
      .hc-chat-panel {
        border-color: @darkBorder;
        .hc-chat-scrollbox {
          background-color: @darkChatPanelBg !important;
          a {
            color: @linkColor;
          }
          .hc-chat-row {
            border-top-color: @lightBorder;
            color: @darkGrayText;
            &:after {
              content: "";
              height: 100%;
              width: 100%;
              position: absolute;
              top: 0;
              left: 0;
              z-index: -1;
            }
          }
          .hc-chat-from {
            color: @darkGrayText;
          }
          .hc-chat-row-failed {
            background-color: @darkFailedBackground;
            .failed-message-actions > a {
              color: @failedMessageActions;
            }
          }
          .date-divider {
            &:before {
              border-bottom-color: @lighterBorder;
            }
            span {
              background-color: @darkChatPanelBg;
              color: @darkGrayText;
            }
          }
          .hc-mention-user {
            background-color: @darkMentionBg;
            border: 1px solid @darkBorder;
            a {
              color: @darkGrayText;
            }
          }
          .hc-mention-user:before {
            color: @darkGrayText;
          }
          .hc-mention-me, .atTagMe {
            background: @darkSelected;
            border: 1px solid @darkSelected;
            color: @darkGrayText;
          }
          .hc-mention-me {
            color: white;
            &:before {
              color: white;
            }
          }

          .rowColors;

          .hc-chat-row.hc-msg-loading,
          .msg-flaky,
          .hc-msg-nocolor.hc-msg-loading,
          .hc-msg-gray.hc-msg-loading,
          .hc-msg-yellow.hc-msg-loading,
          .hc-msg-red.hc-msg-loading,
          .hc-msg-green.hc-msg-loading,
          .hc-msg-purple.hc-msg-loading,
          .hc-msg-blue.hc-msg-loading,
          .hc-msg-video.hc-msg-loading,
          .hc-msg-twitter_status.hc-msg-loading,
          .hc-msg-twitter_user.hc-msg-loading,
          .hc-msg-link.hc-msg-loading,
          .hc-msg-image.hc-msg-loading {
            background-repeat: no-repeat repeat;
            background-position: 0 0;
            background-image: url(data:image/png;base64,);
            &:before {
              background-color: transparent;
            }
          }

          .hc-chat-row.hc-msg-loading,
          .hc-msg-link.hc-msg-loading,
          .hc-msg-image.hc-msg-loading {
            background-repeat: no-repeat repeat;
            background-position: 0 0;
            background-image: url(data:image/png;base64,);
            &:before {
              background-color: transparent;
            }
          }

          .empty{
            color: @darkGrayText;
          }

          .atlascard {
            border-color: @lighterBorder;
            color: @textColor;
            background-color: @darkChatPanelBg;

            a {
              color: @textColor;
            }

            .feedback a {
              color: #3572b0;
            }
          }
        }
      }

      /**
       * Chat Input
       *
       */
      .hc-chat-box {
        border-color: @darkBorder;
        background-color: @darkHeader;
        input {
          background-color: @fromMe;
          color: @darkGrayText;
          border-color: @darkBorder;
        }
        .hc-text-input {
          textarea {
            background-color: @fromMe;
            color: @darkGrayText;
            border-color: @darkBorder;
          }
          .smiley-icon {
            color: @darkGrayText !important;
            &:hover, &.selected {
              color: white !important;
            }
          }
        }
        form.aui {
          .hc-file-btn {
            background: @darkHeader;
            color: @darkGrayText;
            border-color: @darkBorder;

            &:hover, &:active {
              .hc-attach {
                color: white;
              }
            }

            &:hover {
              background: @darkHeader;
            }

            &:active {
              background: @darkBorder;
            }

            &.has-file {
              &:hover {
                border-color: @darkBorder;
              }
              &:active {
                background: @darkHeader;
              }
            }
            .hc-attach {
              color: @darkGrayText;
              &:active {
                background: @darkChatPanelBg;
              }
            }
          }
          .upload-input {
            input {
              border-color: @darkBorder;
            }
          }
          .aui-button, .aui-button:visited, a.aui-button {
            border-color: @darkBorder !important;
            background: @darkHeader;
            .aui-icon {
              color: @darkGrayText;
            }
            &.active {
              box-shadow: inset 0 3px 6px 0 rgba(0, 0, 0, .2);
            }
            &:hover, &.active {
              .aui-icon {
                color: white;
              }
            }
          }
        }
      }

      /**
       * Right Sidebar
       *
       */
      .hc-roster-container {
        background-color: @darkHeader;
        .hc-roster, .hc-files, .hc-links {
          border-color: @darkBorder;
          color: @darkGrayText;
          a {
            color: @linkColor;
          }
          .hc-roster-item, .hc-roster-item a.hc-roster-link {
            color: @darkGrayText;
          }
          .hc-roster-item{
            opacity: .6;
          }
          .hc-roster-item:hover, .hc-roster-item.aui-nav-selected {
            opacity: 1;
          }
        }

        ul.roster-list {
          li.hc-roster-item {
            background: @fromMe;
          }
        }
      }

      /**
       * Mention & Emoticon Autocomplete
       *
       */
      .hc-chat-input-autocomplete {
        .aui-inline-dialog-contents {
          background-color: @darkHeader;
          border-color: @darkBorder;

          ul li.hc-autocomplete-item {
            .hc-ac-name, .hc-ac-mention-name {
              color: @darkGrayText;
            }
            .hc-ac-mention-name {
              opacity: 0.6;
            }
            &.hc-autocomplete-item-selected {
              background-color: @darkHighlight;

              .hc-ac-name, .hc-ac-mention-name {
                color: @white;
              }
            }
          }
        }
        .aui-css-arrow {
          &.aui-bottom-arrow {
            &::before {
              border-color: @darkBorder transparent;
            }
            &::after {
              border-top-color: @darkHeader;
            }
          }
        }
      }

      /**
       * Tooltips
       *
       */
      .hc-tooltip {
        background-color: @darkHeader;
        border-color: @darkBorder;

        a {
          color: @linkColor;
        }

        .arrow {
          &:before {
            border-color: @darkBorder transparent;
          }
          &:after {
            border-color: @darkHeader transparent;
          }
        }

        .smileys-selection {
          img {
            &:hover {
              background-color: @darkHighlight;
            }
          }
        }
      }

      /**
       * Quick Switcher
       *
       */
      .hc-qs{
        .hc-qs-content{
          color: @white;
          background-color: @darkHeader;
          border-color: @darkBorder;

          .hc-qs-input-wrap {
            .hc-qs-input {
              color: @adgLightGray;
              background-color: @darkHeader;
              border-color: @darkHeader;
              &:focus {
                border-color: @darkHeader;
              }
            }
          }

          .hc-qs-list-wrap {
            .hc-qs-item {
              .aui-avatar {
                background-color: @darkInputBg;
              }
              .hc-qs-name {
                color: @adgLightGray;
              }
              &.selected{
                background-color: @qsSelectedRow;
                .hc-qs-name {
                  color: @white;
                }
                .hc-qs-mention-name {
                  color: @adgAshGray;
                }
              }
              &.hc-qs-empty-state{
                color: @darkGrayText;
                p {
                  line-height: 25px;
                  margin: 0;
                }
              }
            }
          }

          .hc-qs-hint {
            .hc-qs-hint-text, .hc-qs-hint-close {
              color: @darkGrayText;
            }
          }
        }
      }

      .hc-chat-from, .hc-classic-neue .hc-chat-msg {
        .sender-name {
          &:hover {
            color: @darkGrayText;
          }
        }
      }

      /**
       * Chat Actions
       */
      .hc-chat-box .input_actions_trigger{
        button#input_actions_dropdown-trigger.hc-dropdown-trigger.aui-dropdown2-trigger.aui-button.aui-button-subtle {

          border-color: @darkBorder;
          background: @darkHeader;

          &.active {
            border-color: @darkBorder;
            background-color: @darkBorder;

            &:hover {
              border-color: @darkBorder;
              background-color: @darkBorder;
            }
          }

          &:hover {
            background-color: @darkHeader;
            border: 1px solid @darkBorder;
          }
        }
      }
      //Specific background colors was added in order to fix issue with broken Gif avatars in Chrome Version 53.
      //The fix is to give Gif avatars containers a background color.
      .aui-avatar-xsmall {
        .aui-avatar-img {
          .avatar-image-helper(16px, @darkAvatarColor);
        }
      }
      .aui-avatar-small {
        .aui-avatar-img {
          .avatar-image-helper(24px, @darkAvatarColor);
        }
      }
      .aui-avatar-medium {
        .aui-avatar-img {
          .avatar-image-helper(32px, @darkChatPanelBg);
        }
      }
      .aui-avatar-large {
        .aui-avatar-img {
          .avatar-image-helper(48px, @darkAvatarColor);
        }
      }
      .aui-avatar-xlarge {
        .aui-avatar-img {
          .avatar-image-helper(64px, @darkAvatarColor);
        }
      }
      .aui-avatar-xxlarge {
        .aui-avatar-img {
          .avatar-image-helper(96px, @darkAvatarColor);
        }
      }
      nav {
        .aui-avatar-small {
          .aui-avatar-img {
            .avatar-image-helper(24px, @defaultAvatarColor);
          }
        }
        .aui-dropdown2-active, a:hover {
          .aui-avatar-small {
            .aui-avatar-img {
              .avatar-image-helper(24px, @selectedListitem);
            }
          }
        } 
      }
      .hc-right-sidebar-col .aui-nav-selected {
        .aui-avatar-small {
          .aui-avatar-img {
            .avatar-image-helper(24px, @bgactive);
          }
        }
        .aui-avatar-xsmall {
          .aui-avatar-img {
            .avatar-image-helper(16px, @bgactive);
          }
        }
      }
      .hc-main-col header {
        .aui-avatar-large {
          .aui-avatar-img {
            .avatar-image-helper(48px, @darkSmallAvatar);
          }
        }
      }
      .hc-autocomplete-item, .hc-lobby-list-item.selected {
        .aui-avatar-small {
          .aui-avatar-img {
            .avatar-image-helper(24px, @darkSmallAvatar);
          }
        }
      }
      .hc-autocomplete-item-selected{
        .aui-avatar-small {
          .aui-avatar-img {
            .avatar-image-helper(24px, @darkHighlight);
          }
        }
      }
      .hc-msg-blue {
        .aui-avatar-medium {
          .aui-avatar-img {
            .avatar-image-helper(32px, @darkInputBg);
          }
        }
      }
    }

    #room-actions-drop-down, .aui-style-default.aui-dropdown2.hc-dropdown-container {
      background: @fromMe;
      color: @darkGrayText;
      border-color: @darkBorder;
      a {
        color: @darkGrayText;
      }
      .active {
        color: #fff;
      }
      .disabled {
        opacity: .6;
      }
      .aui-dropdown2-section + .aui-dropdown2-section {
        border-color: @darkBorder;
      }
      .aui-dropdown2-radio {
        &.checked {
          background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAOklEQVQ4y2P4//8/AyWYYdQAnAawAXE7ED+H4naoGNEGgDSgg3ZSDHiOxYDndDWAYi9QHIijKZEEDABrgRimRc6sKAAAAABJRU5ErkJggg==");
        }
      }
    }

    /**
    * Message Actions
    */

    div.actionable-msg-container.msg-line  {

      .hc-dropdown .aui-button span{
        color: @darkButtonBg;
      }

      .hc-dropdown .aui-button:hover {
        span {
          color: @darkGrayText;
        }
        background-color: transparent;
        border-color: @darkBorder;
      }

      &.hc-message-actions-active .hc-dropdown .aui-button.aui-dropdown2-trigger.aui-dropdown2-trigger-arrowless.hc-dropdown-trigger {
        span {
          color: @white;
        }

        background-color: @darkBorder;
        border-color: @darkBorder;
      }
    }

    /**
     * Input actions
     */

    #hc-chat-actions-arrow {
      &::before, &::after {
        border-color: @darkBorder transparent;
      }

      &:after {
        border-top-color: @darkInputBg;
      }
    }

    /**
     * Emoticons
     */
    img[alt="(disapproval)"],
    img[alt="(hipster)"],
    img[alt="(shrug)"],
    img[alt="(tableflip)"] {
      filter: invert(100%);
    }

    #video-chat-service-menu {
      background: @fromMe;
      border-color: @darkBorder;
      a {
        color: @darkGrayText;
      }
      .active {
        color: #fff;
      }
    }

    .hc-default-avatar {
      color: #333;
    }

    /* Code blocks */
    .hc-chat-msg .msg-status .msg-line {
      &.truncatable.code div.truncatable,
      &.truncatable.monospace div.truncatable,
      div.pre-wrap {
        border-color: @lighterBorder;
      }
    }

    .file-error-message {
      background-color: @darkChatPanelBg;
      color: @textColor;
      border-color: @lighterBorder;

      h3 {
        color: @fileErrorColor;
      }
    }
  }
}

/*
 * Embedded Search Panel
 *
 */
body.embedded-search-page {
  background-color: @black;
  color: @darkGrayText;

  &.display_mode_minimal .search-box {
    background-color: @darkHeader;
    color: @darkGrayText;
    border-bottom-color: @darkChatPanelBg;
  }
  .aui-page-panel {
    background-color: @black;
  }
  a {
    color: @linkColor;
  }
  .adg .search-result-header {
    border: none;
  }
  .search-result-header h3 {
    color: @darkGrayText;
    font-weight: normal !important;
  }
  .hc-chat-row {
    border-color: @darkChatPanelBg;
  }
  .hc-msg-nocolor {
    background-color: @fromMe;
  }

  // .hc-msg-{color}
  .rowColors;

  .hc-chat-row{

    border-left: 4px solid;

    .hc-chat-from-color() {
      opacity: 1;
      .hc-chat-from {
        color: @subdued;
        &:hover{
          color: @textColor;
        }
      }
    }

    &.hc-msg-gray {
      border-left-color: @lightBorder;
      background: #1A1D20;
      .hc-chat-from-color;
    }
    &.hc-msg-yellow {
      border-left-color: @darkYellow;
      background: #252318;
      .hc-chat-from-color;
    }
    &.hc-msg-red {
      border-left-color: @darkRed;
      background: #281819;
      .hc-chat-from-color;
    }
    &.hc-msg-green {
      border-left-color: @darkGreen;
      background: #13281E;
      .hc-chat-from-color;
    }
    &.hc-msg-purple {
      border-left-color: @darkPurple;
      background: #14192C;
      .hc-chat-from-color;
    }
    &.hc-msg-blue {
      background: #1C2227;
      .hc-chat-from-color;
    }
  }


  .search-results-container .hc-chat-row .hc-chat-msg .highlight,
  .history_container .hc-chat-row .hc-chat-msg .highlight {
    background-color: @darkYellow;
    border-color: @darkYellow;
    color: @white;
  }
  .atTag {
    background-color: @darkMentionBg;
    border: 1px solid @darkChatPanelBg;
    color: @darkGrayText;
  }
  .atTag.atTagMe {
    background: @darkSelected;
    border: 1px solid @darkSelected;
    color: @white;
  }
  .aui-icon {
    color: @darkDefaultAuiIcon;
  }
  .no-results h3 {
    color: @darkGrayText;
  }
  .adg #search-form .select2-container {
    background-color: @white;
  }
  form.aui .aui-select2-container {
    background-color: @darkHeader;
  }

  .prettyprint {
    background: #EFEFEF;
    span {
      font-family: Menlo, 'Bitstream Vera Sans Mono', 'DejaVu Sans Mono', Monaco, Consolas, monospace;
      font-size: 12px;
    }
  }
  .prettify-colors-dark();

}
