.ellipsis {
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@left-sidebar-width:220px;
@right-sidebar-width:200px;

.box-shadow (@string) {
  box-shadow: @string;
}

.vert-gradient (@start, @end) {
  background-color: @start;
  background-image: linear-gradient(to bottom, @start, @end);
}

.user-select (@string) {
  -webkit-user-select: @string;
  -moz-user-select: @string;
  -ms-user-select: @string;
  -o-user-select: @string;
  user-select: @string;
}

.font-smoothing () {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.avatar-helper (@fontSize, @lineHeight) {
  font-size: @fontSize;
  line-height: @lineHeight;
  width: @lineHeight;
  height: @lineHeight;
}

.avatar-image-helper (@size, @avatarColor) {
  border-radius: @size;
  width: @size;
  height: @size;
  background-size: cover;
  background-position: 50% 50%;

  background-color: @avatarColor;
  display: block;
}