#hipchat, body {

  .hc-right-sidebar-col {
    transform: translate3d(0, 0, 0);
    position: absolute;
    right: 0;
    width: 100%;
    height: 100%;
    .hc-roster-container{
      height: 100%;
    }
    .resize-handle {
      height: 100%;
      position: absolute;
      left: -5px;
      top: 0;
      cursor: col-resize;
      width: 10px;
      z-index: 3;
    }
    .hc-spinner {
      position: absolute;
      top: 50%;
      left: 50%;
    }
    div.roster-wrap {
      display: block;
      height: calc(~'100% - 10px');
      &.guest-access-enabled {
        height: calc(~'100% - 60px - 10px');
      }
      ul.roster-list {
        display: block;

        li.hc-roster-item {
          display: block;
          background: @white;
          overflow: hidden;

          &.aui-nav-selected {
            background: @bgactive;
          }
        }
      }
      .hc-roster-mini {
        width: 100%;
        padding: 0;
        .aui-avatar {
          width: @people-glance-avatar-size;
          height: @people-glance-avatar-size;
        }
      }
      div.roster-mini-flex-list{
        .flex-display();
        .flex-direction(row);
        .flex-wrap(wrap);
        .flex-grow(1);
        div {
          &[data-order="show"]{
            order:1;
          }
          &[data-order="expander"]{
            order:2;
          }
          &[data-order="overflow"]{
            order:3;
          }
        }
        padding: 0;
        margin: 0 @people-glance-right-margin 0 0;
        max-height: @people-glance-avatars-max-height;
        .hc-roster-mini-item {
          margin: @people-glance-avatar-row-margin 0;
        }
        .hc-roster-mini-expander {
          margin: @people-glance-avatar-row-margin 0;
          .hc-default-avatar {
            background-color: @spinnerbg;
            color: @text;
            font-size: 10px;
            font-weight: bold;
          }
        }
        .aui-avatar {
          margin: 0 @people-glance-avatar-margin 0 0;
        }
        .aui-icon {
          fill: @icon;
        }
      }
    }
    .hc-roster, .hc-files, .hc-links {
      width: 100%;
      padding: 5px 0;
      .aui-nav-heading {
        margin-top: 0;
        padding: 15px 10px 4px 10px;
        border-top: none;
      }
    }
    .hc-links {
      .hc-roster-item {
        line-height: 16px;
      }
    }
    .hc-files {
      .hc-roster-item {
        line-height: 13px;
      }
    }
    .hc-files, .hc-links {
      position: absolute;
      height: 100%;
      .hc-roster-item {
        font-size: 11px;
        .hc-file-link, .hc-link {
          .ellipsis();
          display: block;
        }
        .hc-file-link {
          position: relative;
          top: -3px;
        }
        .hc-file-icon {
          margin-right: 5px;
          position: relative;
          top: 2px;
        }
        .hc-file-name {
          vertical-align: text-top;
        }
        .hc-roster-user-name, .hc-roster-date {
          .ellipsis();
          cursor: default;
          width: 48%;
          display: block;
          float: left;
          text-align: left;
        }
        .hc-roster-date {
          padding-left: 4%;
          text-align: right;
        }
        &.hc-spinner-item{
          position: relative;
        }
      }
    }
    .hc-roster {
      height: 100%;
      .hc-roster-item {
        box-sizing: border-box;
        line-height: 16px;
        .aui-avatar-small ~ .hc-roster-link {
          line-height: 24px;
        }
        .aui-avatar-small ~ .hc-roster-status {
          margin-top: -6px;
          padding-left: 32px;
        }
      }

      .hc-roster-link {
        font-size: 13px;
        vertical-align: top;
      }
      .hc-roster-status {
        color: @rosterStatusText;
        font-size: 10px;
        line-height: 12px;
        padding-left: 25px;
        .ellipsis();
      }
      .hc-roster-admin {
        font-weight: bold;
      }
      .aui-nav-heading {
        box-sizing: border-box;
        strong {
          text-transform: uppercase;
        }
      }
      .hc-roster-xlarge-msg {
        border-left: 2px solid @orange;
        padding-left: 10px;
        margin: 5px 10px;
        font-size: 12px;
        color: @subdued;
      }
    }

    .hc-tab-es {
      margin-top: 40px;
      text-align: center;
      width: 100%;
      .hc-tab-es-img {
        margin: 10px auto 30px;
        &.roster {
          height: 62px;
          width: 70px;
          margin-bottom: 25px;
        }
        &.links, &.files {
          height: 66px;
          width: 73px;
        }
        &.integrations {
          margin-bottom: 10px;
          img {
            max-width: 90%;
            max-height: 90%;
          }
        }
      }
      .hc-tab-es-title {
        cursor: default;
        font-size: 12px;
        font-weight: bold;
        text-transform: uppercase;
        line-height: 20px;
      }
      .hc-tab-es-msg {
        margin: auto;
        width: 75%;
      }
    }
    .hc-roster-item {
      color: @rosterItemText;
      .aui-nav-item {
        .ellipsis();
        padding: 5px 10px;
      }
      a {
        &:hover {
          text-decoration: none;
        }
        &.hc-roster-link {
          color: @text;
        }
      }
      &.aui-nav-selected {
        background: @bgactive;
        .hc-roster-link, .hc-roster-status {
          color: @white !important;
        }
      }
    }
    .hc-sidebar-footer {
      width: 100%;
      .hc-footer-inner {
        padding: 11px 12px;
      }
    }

    .guest-access {
      display: block;
      bottom: 0;
      width: 100%;
      color: #fff;
      background: @bgblue;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-align: center;
      padding: 10px;
      box-sizing: border-box;
      overflow: hidden;
      a {
        color: #fff;
        width: 100%;
        display: block;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
    }
  }

  .hc-right-sidebar-col {
    .hc-integrations {
      position: absolute;
      width: 100%;
      height: 100%;
      overflow-y: hidden;
      background-color: @bggray;

      &.glance-selected {
        overflow-y: hidden;
      }

      .hc-integrations-wrap {
        position: absolute;
        width: 100%;
        top: 0;
        bottom: 0;
        overflow-y: auto;

        &.hc-can-manage-integrations {
          bottom: @chat-box-height;
        }
      }

      .hc-glances {

        .hc-glance {
          margin: 10px;
          padding: 5px 20px;
          background-color: white;
          border: 1px solid @bordergray;
          border-radius: 3px;

          &.clickable {
            cursor: pointer;
          }

          &.error {
            background-color: @bggray;
          }
        }

        .hc-integration-advertisement {
          background-color: transparent;
          border-style: dashed;
          border-color: @adgOrange;
        }

        .hc-discovery-glance-close {
          display: none;
          position: absolute;
          right: 10px;
          margin-top: 6px;
        }

        .hc-integration-advertisement:hover .hc-discovery-glance-close {
          display: inline;
        }
      }

      .hc-integration-view-sidebar {

        .hc-glance {

          .aui-nav-item {
            margin-right: 10px;
          }
        }

        div.roster-wrap {
          display: block;
          height: calc(~'100% - 40px - 10px');
          &.guest-access-enabled {
            height: calc(~'100% - 60px - 40px - 10px');
          }
        }

        .hc-links, .hc-files {
          height: auto;
          top: 40px;
          bottom: 0;
        }

      }


      .hc-glance {

        .aui-nav-item {
          line-height: 28px;
          .ellipsis();
        }

        .aui-icon {
          margin-right: 10px;
        }

        &.loading {
          color: @subdued;
          padding-right: 10px;

          .hc-spinner-container {
            margin-right: 10px;

            .hc-spinner {
              position: inherit;
            }
          }
        }

        .hc-glance-content {
          .flex-display();
          .flex-direction(row);
          .align-items(center);

          .aui-icon {
            .flex(0 0 16px);
            color: @icon;
          }
        }

        .hc-glance-label {
          .flex(0 1 auto);
          .ellipsis();
          display: block;
          min-width: 0;
        }

        .hc-glance-status {
          .flex(1 0 main-size);
          .flex-shrink(0);
          display: block;
          margin-left: auto;

          .aui-icon {
            margin: 0;
          }

          .hc-glance-status-inner {
            margin: 0 0 0 10px;
          }
        }

        &:hover:not(.error),
        &:active:not(.error) {
          border-color: @buttonblue;
        }

        &.error {

          .aui-nav-item {
            line-height: inherit;
            text-overflow: clip;
          }

          .aui-icon {
            vertical-align: middle;
          }

          .hc-glance-content {
            display: inline-block;
            vertical-align: middle;
            min-width: 115px;

            .hc-glance-label, .hc-glance-status {
              line-height: 14px;
            }

            .hc-glance-status {
              font-size: 12px;
              color: @subdued;

              .aui-button-link {
                height: initial;
                margin-left: 10px;
                padding: 0;
                border: 0;
                font-size: 12px;
                line-height: 14px;
                min-width: 50px;
              }
            }
          }

        }

      }

      .hc-integration-view-sidebar {
        .flex-display();
        .flex-direction(column);

        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: white;

        .hc-integration-sidebar-header {
          border-bottom: 1px solid @bordergray;

          .hc-glance-label {
            cursor: default;
          }
        }

        .hc-integration-sidebar-header, .hc-integration-sidebar-footer {
          box-sizing: border-box;
          height: 40px;
          padding: 5px 10px 10px 10px;

          .close {
            position: absolute;
            top: 12px;
            right: 10px;
            cursor: pointer;
            color: @adgGray;

            &:hover {
              color: @text;
            }
          }
        }

        iframe {
          .flex(1);
          border: 0;
          width: 100%;
        }

        .hc-integration-view-name {
          line-height: 1.5625;
          width: 94%;
          .ellipsis();
        }
      }

      .hc-integrations-configure-button {
        box-sizing: border-box;
        position: absolute;
        width: 100%;
        bottom: 0;
        height: @chat-box-height;
        border-top: solid 1px @bordergray;
        line-height: @chat-box-height;
        padding: 0 10px 0 10px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;

        a {
          width: 100%;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;

          &:hover, &:active, &:focus {
            text-decoration: none;
          }

          .aui-icon {
            margin-right: 8px;
          }
        }
      }
    }
  }

  .hc-tooltip-helper .aui-inline-dialog-arrow.aui-bottom-arrow {
    top: auto;
    left: 56px;
    bottom: -14px;
    &:before {
      border-color: @adgOrange transparent;
    }
  }

  .hc-tooltip-helper {
    border: 2px solid @adgOrange;
  }

  .add-integrations-tooltip {
    color: @adgMediumGray;

    .add-integrations-tooltip-title {
      font-weight: bold;
    }
  }
}

.groupchat-room-dropdown {

  .hc-integration-warning-icon {
    margin-top: 2px;
    color: @orange;
  }

  li:hover .hc-integration-warning-icon,
  a.active .hc-integration-warning-icon {
    color: @white;
  }

  .integrations-user-action a {
    display: flex;

    span {
      flex: 0 1 auto;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .hc-integration-warning-icon {
      flex: 0 0 auto;
      margin-left: auto;
    }
  }
}

.hc-integrations-error {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;

  .hc-integrations-error-image {
    width: 130px;
    height: 80px;
  }

  p {
    margin-bottom: 8px;
    font-weight: bold;
  }
}
