#current-user-status {

  .hc-availability {
    ul > li > a {
      padding-left: 10px;
      svg {
        margin-right: 2px;
      }
    }

    span + span {
      padding-left: 1px;
    }
  }

}

.status-dropdown {
  position: relative;
}

#userStatusMessage {
  right: 19px;
  width: 250px;
  display: none;

  .aui-inline-dialog-contents {
    padding: 15px;
  }

  .aui-css-arrow {
    right: 30px;
  }

  .aui-item.status-icon {
    width: 13px;
    padding-top: 7px;
  }

  .aui-item.status-message {
    width: 150px;
    padding: 0 0 0 10px;
  }

  .aui-item:last-child {
    padding: 0;

    button {
      margin: 0 0 0 10px
    }
  }
}