#hipchat, body {

  .aui-page-header {
    color: #707070;
    background-color: @bggray;
    border-bottom: solid 1px @bordergray;
    .user-select(none);
    &.lobby-header {
      padding: 10px;
      min-height: @lobby-header-height;
      .user-select(none);
      .aui-page-header-actions {
        .aui-buttons {
          margin: 5px 0 5px 10px;
        }
      }
    }
    &.room-header {
      padding: 10px;
      box-sizing: border-box;
      height: 69px;
      max-height: 69px;
      form.aui {
        margin-top: -2px;
        margin-left: -1px;
        input.text {
          margin-top: -1px;
          margin-left: -5px;
          max-width: none;
          height: 24px;
          // (24px button width + 10px margin left for button) * 2
          width: calc(~'100% - 68px');
          vertical-align: top;
          line-height: 16px; // input height - vertical padding (IE fix)
        }
        .aui-button {
          margin: -1px 0 0 10px;
          box-sizing: border-box;
          height: 24px;
          width: 24px;
          vertical-align: top;
          padding: 2px 0;
          text-align: center;
          span {
            width: 16px;
            cursor: pointer;
            height: 14px;
            &.icon-close {
              width: 13px;
            }
            &:after {

            }
          }
        }
      }
      .aui-page-header-inner {
        height: 48px;
        display: block;
      }
      h3 {
        color: #707070;
        display: inline;
      }
      p {
        margin: 0;
        line-height: 17px;
        .hc-status-icon {
          margin-right: 4px;
          vertical-align: sub;
        }
        img {
          vertical-align: middle;
        }
      }
      .hc-priv-chat {
        .aui-page-header-main {
          display: inline-block;
          vertical-align: top;
          width: calc(~'100% - 150px');
          span.mention-name {
            color: #999999;
          }
        }
      }
      .hc-groupchat {
        .aui-page-header-main {
          vertical-align: top;
          width: calc(~'100% - 300px');
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          display: inline-block;

          &.hc-integrations-enabled {
            width: calc(~'100% - 220px');
          }

          div {
            position: relative;
          }
          div, span {
            white-space: nowrap;
            text-overflow: ellipsis;
            cursor: default;
            width: 100%;
            span {
              vertical-align: middle;
              display: inline-block;
              height: 100%;
              overflow: hidden;
              img {
                vertical-align: middle;
              }
            }
          }
          .aui-lozenge {
            width: auto;
            margin-left: 5px;
          }
        }
        .hc-groupchat-header {
          display: flex;
          overflow: hidden;

          h3 {
            flex: 0 1 auto;
            overflow: hidden;
            text-overflow: ellipsis;
          }

          .guest-access-lozenge {
            flex-shrink: 0;
            margin-top: 4px;
            z-index: 1;
          }
        }

        .topic-spinner {
          position: relative;
          height: 20px;
          width: 20px;
          top: 4px;
        }
      }
      .hc-priv-chat {
        .aui-page-header-main {
          p {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            cursor: default;
            display: inline-block;
            width: 97%;
            font-size: 12px;
            font-weight: 400;
          }
        }
      }

      .page-header-icon {
        display: inline-block;
      }

      .aui-page-header-actions {
        white-space: nowrap;
        width: 230px;
        display: inline-block;
        position: absolute;
        right: 20px;
        top: 20px;
        .aui-buttons {
          margin: 0px 0 0px 10px;

          .notification-badge {
            position: relative;
            overflow: visible;

            &:after {
              content: '';
              display: block;
              position: absolute;
              top: -5px;
              right: -5px;
              width: 10px;
              height: 10px;
              background: @orange;
              border-radius: 100%;
            }
          }
        }

        &.hc-integrations-chat-header-actions {
          width: auto;
        }
      }
    }
  }

  .hc-chat-header {
    p {
      color: #666;
    }
  }
  .hc-user-photo .aui-icon {
    color: @white;
  }
  .aui-icon.hc-user-silhouette {
    width: 24px;
    height: 24px;
    border-radius: 3px;
  }

  .aui-icon.hc-priv-chat {
    width: 48px;
    height: 48px;
    border-radius: 48px;
    &.hc-user-avatar {
      background-size: cover !important;
      background-position: 50% 50%;
    }
  }

  .page-header-icon {
    width: 48px;
    display: table-cell;
    .hipchat-icon-huge {
      width: 48px;
      height: 48px;
      color: #999999;
      border-radius: 48px;
      position: relative;
      &:after {
        speak: none;
        font-size: 48px !important;
        font-style: normal !important;
        .font-smoothing();
        margin-top: -24px !important;
      }
    }
  }

  .hc-page-header-main {
    padding-top: 2px;
    padding-left: 15px;
    h3 {
      .user-select(text);
      cursor: text;
      font-size: 16px;
      font-weight: 500;
    }
    .mention-name {
      .user-select(text);
      cursor: text;
    }
  }

  .hc-page-header-main {
    padding-top: 2px;
    padding-left: 15px;
    h3 {
      font-size: 16px;
      font-weight: 500;
    }
  }

  .hc-header-user-avatar {
    padding-right: 0;
    text-align: center;
    min-width: 24px;
    position: relative;
    .hc-user-photo {
      display: block;
      img {
        display: inline;
        border-radius: 100%;
      }
    }
    .hc-status-icon {
      top: 14px;
      right: 6px;
      position: absolute;
      display: inline;
    }
    .aui-avatar-project {
      .box-shadow(none);
      background-color: transparent;
    }
    .aui-avatar-project:before {
      border: 0;
    }
  }

  .aui-page-header-image.hc-header-user-avatar > .hc-close-icon {
    width: 9px;
    top: 2px;
    &:hover {
      color: #333;
    }
  }

}

.hc-oto-title:after,
.hc-oto-status:after,
.hc-oto-time:after {
  content: '\b7';
  margin: 0 10px;
  speak: none;
}
